import { useContext, useEffect, useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components/macro';
import { toast } from 'react-toastify';
import { Button, VerticalLayout, Typography } from '../components/ui';
import Layout from '../components/general/Layout';
import spacing from '../components/ui/style/spacing';
import palette from '../components/ui/style/palette';
import { respondTo } from '../components/ui/style/dimensions';
import { AppContext } from '../contexts/AppContext';
import WellbeingSlider, {
  LENGTH_SLIDER,
} from '../components/Onboarding/WellbeingSlider';
import { patchOnboardingComplete } from '../services/api.service';
import { AppActions } from '../contexts/app.reducer';

type LocationState = {
  backToSurvey?: boolean;
};

const StyledNextButton = styled(Button)`
  ${respondTo.desktopUp} {
    min-width: 400px;
  }
`;

const Home: React.FC = () => {
  const history = useHistory();
  const { state: locationState } = useLocation<LocationState>();
  const { state, dispatch } = useContext(AppContext);
  const [firstLogin, setFirstLogin] = useState<boolean | undefined>(true);
  const [disableButton, setDisableButton] = useState<boolean | undefined>(true);
  const [loading, setLoading] = useState(false);
  const { t } = useTranslation();

  const isBackToSurveyMode = locationState && locationState.backToSurvey;

  const onPatchOnboardingComplete = async () => {
    try {
      setLoading(true);
      const { result } = await patchOnboardingComplete(state.studentProfile.ID);
      dispatch({
        type: AppActions.updateOnboardingComplete,
        payload: result,
      });
    } catch (error) {
      toast.error(t('An error occured when submiting onboarding!'));
    } finally {
      setLoading(false);
    }
  };
  const handleNextButton = async () => {
    if (!state.studentProfile.onboardingComplete) {
      await onPatchOnboardingComplete();
    }
    history.push('/survey');
  };
  useEffect(() => {
    setDisableButton(!state.studentProfile.onboardingComplete);
    setFirstLogin(!state.studentProfile.onboardingComplete);
  }, [state.studentProfile.onboardingComplete]);

  const onChangeSlider = (index: number) => {
    if (
      index === LENGTH_SLIDER - 1 &&
      !state.studentProfile.onboardingComplete
    ) {
      setDisableButton(false);
    }
  };

  return (
    <Layout responsive color="light">
      <VerticalLayout align="center" childGap={spacing.xLarge}>
        <VerticalLayout align="center" childGap={spacing.smallPlus}>
          {!isBackToSurveyMode && (
            <Typography
              size="mediumLg"
              color={palette.purple.p4}
              align="center"
              responsive
            >
              Welcome, {state.studentProfile.firstName}
            </Typography>
          )}
          <Typography
            weight="bold"
            responsive
            size="xxLarge"
            color={palette.purple.p4}
          >
            {t('Why wellbeing matters')}
          </Typography>
          <Typography responsive color={palette.purple.p4}>
            {t(
              'Learn about your wellbeing and why it is important for your learning'
            )}
          </Typography>
        </VerticalLayout>
        <WellbeingSlider afterChange={onChangeSlider} />
        <StyledNextButton
          loading={loading}
          responsive
          onClick={handleNextButton}
          disabled={disableButton}
        >
          {t(!firstLogin ? 'Close' : 'Start survey')}
        </StyledNextButton>
      </VerticalLayout>
    </Layout>
  );
};

export default Home;
