import styled from 'styled-components/macro';
import { HorizontalLayout } from '../ui';
import spacing from '../ui/style/spacing';
import { respondTo } from '../ui/style/dimensions';
import palette from '../ui/style/palette';

type HeaderProps = {
  logo?: React.ReactNode;
  color?: string;
  bgColor?: string;
  actions?: React.ReactNode;
  showHeaderActions?: boolean;
  hideLogo?: boolean;
};

const HeaderStyle = styled.div<HeaderProps>`
  padding: 35px 20px;
  width: 100%;
  ${respondTo.tabletLSUp} {
    padding: 35px 70px;
  }
  ${respondTo.desktopUp} {
    padding: 35px 120px;
  }
  background-color: ${props => props.bgColor || palette.purple.p1};
  color: ${props => props.color || palette.white};
`;

const Header: React.FC<HeaderProps> = ({
  logo,
  color,
  bgColor,
  actions,
  showHeaderActions,
  hideLogo = false,
}) => {
  return (
    <HeaderStyle bgColor={bgColor} color={color} data-testid="wb-header">
      <HorizontalLayout justify="space-between" childGap={spacing.medium}>
        {!hideLogo && logo}
        {showHeaderActions && (
          <HorizontalLayout
            data-testid="wb-header-actions"
            justify="flex-end"
            align="center"
          >
            {actions}
          </HorizontalLayout>
        )}
      </HorizontalLayout>
    </HeaderStyle>
  );
};

export default Header;
