import palette from '../style/palette';
import spacing from '../style/spacing';
import Typography from '../Typography';
import VerticalLayout from '../VerticalLayout';

type IconProps = {
  icon: string;
  text?: string;
  alt?: string;
};

const IconWithText: React.FC<IconProps> = ({ text, alt, icon }) => {
  return (
    <VerticalLayout align="center" childGap={spacing.medium}>
      <img src={icon} alt={alt || text} />
      <Typography align="center" color={palette.purple.p4} size="mediumLg">
        {text}
      </Typography>
    </VerticalLayout>
  );
};

export default IconWithText;
