export const breakpoints = {
  // mobile first for small devices < 576
  mobileLanscapeWidth: 576, // width >= 576 and < 768
  tabletWidth: 768, // width >= 768 and < 992
  tabletLanscapeWidth: 992, // width >= 992 and < 1200
  desktopWidth: 1200, // width >= 1200 and < 1336
  mediumDesktopWidth: 1336, // width >= 1336 and < 1440
  largeDesktopWidth: 1440, // width >= 1440
};

export const respondTo = {
  onlyMobile: `@media only screen and (max-width: ${breakpoints.mobileLanscapeWidth}px)`,
  mobileUp: `@media only screen and (mix-width: ${breakpoints.mobileLanscapeWidth}px)`,
  tabletUp: `@media only screen and (min-width: ${breakpoints.tabletWidth}px)`,
  tabletLSUp: `@media only screen and (min-width: ${breakpoints.tabletLanscapeWidth}px)`,
  tabletLSDown: `@media only screen and (max-width: ${breakpoints.tabletLanscapeWidth}px)`,
  desktopUp: `@media only screen and (min-width: ${breakpoints.desktopWidth}px)`,
  mediumDesktopUp: `@media only screen and (min-width: ${breakpoints.mediumDesktopWidth}px)`,
  largeDesktopUp: `@media only screen and (min-width: ${breakpoints.largeDesktopWidth}px)`,
};
