import axios, { AxiosInstance } from 'axios';
import { Config } from '../util/helpers';
import { getLocalStorage, LocalStorage } from './storage.service';
import {
  CreateSurveyResponse,
  Student,
  SchoolClass,
  StudentQuestion,
  CheckAnswer,
  DistressLogPayload,
  OnboardingComplete,
} from './api.types';

const requestConfig = {
  baseURL: Config.API_URL,
  timeout: 10000,
};

const axiosInstance: AxiosInstance = axios.create(requestConfig);

axiosInstance.interceptors.request.use(config => {
  const token = getLocalStorage(LocalStorage.AUTH_TOKEN);
  if (token) {
    Object.assign(config.headers, {
      Authorization: `Bearer ${token}`,
    });
  }
  return config;
});

export const login = async (
  cycle: string,
  username: string
): Promise<{ token: string; student: Student }> => {
  const { data } = await axiosInstance.post('/auth/student-login', {
    cycle,
    username,
  });
  return data;
};

export const saveResponse = async (body: CreateSurveyResponse) => {
  const { data } = await axiosInstance.post(`/answers`, body);
  return data;
};

/**
 * Get a student profile
 * @param providerId The id of the student
 * @returns
 */
export const getStudentProfile = async (
  providerId: string
): Promise<Student> => {
  const { data } = await axiosInstance.get(`/students/${providerId}`);
  return data.student;
};

/**
 * Get a class information using the class ID
 * @param classId the classID
 * @returns
 */
export const getClassDetails = async (
  classId: string
): Promise<SchoolClass> => {
  const { data } = await axiosInstance.get(`/classes/${classId}`);
  return data.data;
};

/**
 * Get questions for the current itertion
 * @param cycle The cycle ID
 * @returns
 */
export const getIterationQuestions = async (
  cycle: string
): Promise<StudentQuestion> => {
  const { data } = await axiosInstance.get(`/cycle/${cycle}`);
  return data.data;
};

/**
 * Check iteration answered
 * @param studentId The ID of the student
 * @param iterationId The iterationID
 * @returns true/false
 */
export const getCheckAnswer = async (
  studentId: string,
  iterationId: string
): Promise<CheckAnswer> => {
  const { data } = await axiosInstance.get(
    `/answers/check?studentId=${studentId}&iterationId=${iterationId}`
  );
  return data;
};

/**
 * Send distress notification
 * @param payload The distressLogPayload
 * @returns data The new id of added record
 */
export const sendDistressNotification = async (
  payload: DistressLogPayload
): Promise<string> => {
  const {
    data: { data },
  } = await axiosInstance.post('/distress', payload);
  return data;
};

/**
 * Patch onboarding complete
 * @param payload The patchOnboardingComplete
 * @returns boolean
 */
export const patchOnboardingComplete = async (
  studentId: string
): Promise<OnboardingComplete> => {
  const { data } = await axiosInstance.patch(`/students/${studentId}`);
  return data;
};
