import classNames from 'classnames';
import styled, { css } from 'styled-components/macro';
import { px2em } from '../../util/helpers';

type ComponentsProps = {
  align?: string;
  inline?: boolean;
  justify?: string;
  childGap?: number;
  fullHeight?: boolean;
  responsive?: boolean;
  flexDirection?: string;
  className?: string;
};

type StyledProps = {
  theme: ComponentsProps;
};

const Root = styled.div<StyledProps>`
  display: ${props => (props.theme.inline ? 'inline-flex' : 'flex')};
  flex-direction: ${props => props.theme.flexDirection};
  width: ${props => (props.theme.inline ? 'auto' : '100%')};
  align-items: ${props => props.theme.align};
  justify-content: ${props => props.theme.justify};
  color: inherit;
  ${props =>
    props.theme.fullHeight &&
    css`
      height: 100%;
    `}
  & > * + * {
    margin-top: ${props =>
      props.theme.responsive
        ? px2em(props.theme.childGap)
        : `${props.theme.childGap}px`};
  }
`;

const VerticalLayout: React.FC<ComponentsProps> = ({
  children,
  align = 'flex-start',
  justify = 'flex-start',
  childGap = 0,
  inline = false,
  fullHeight = false,
  responsive,
  flexDirection = 'column',
  className,
  ...rest
}) => {
  return (
    <Root
      {...rest}
      className={classNames(`${childGap ? `hasChildren` : ''}`, className)}
      theme={{
        inline,
        align,
        justify,
        childGap,
        fullHeight,
        responsive,
        flexDirection,
      }}
    >
      {children}
    </Root>
  );
};

export default VerticalLayout;
