import { ButtonHTMLAttributes } from 'react';
import { StyledButton } from './styles';

type ClickableProps = ButtonHTMLAttributes<HTMLButtonElement> & {
  readOnly?: boolean;
  className?: string;
};

const Clickable: React.FC<ClickableProps> = ({
  onClick,
  children,
  readOnly = false,
  className,
}) => {
  return (
    <StyledButton
      data-testid="clickable"
      type="button"
      onClick={onClick}
      disabled={readOnly}
      readOnly={readOnly}
      className={className}
    >
      {children}
    </StyledButton>
  );
};

export default Clickable;
