import { useMediaQuery } from '@material-ui/core';
import { createContext, useContext, useEffect, useState } from 'react';
import { respondTo } from '../components/ui/style/dimensions';

type ResponsiveBreakPoint = {
  onlyMobile: boolean;
  onlyTablet: boolean;
  tabletUp: boolean;
  onlyTabletLS: boolean;
  tabletLSUp: boolean;
  onlyDesktop: boolean;
  desktopUp: boolean;
  onlyMediumDesktop: boolean;
  mediumDesktopUp: boolean;
  largeDesktop: boolean;
};

const ResponsiveContext = createContext<ResponsiveBreakPoint>({
  onlyMobile: false,
  onlyTablet: false,
  tabletUp: false,
  onlyTabletLS: false,
  tabletLSUp: false,
  onlyDesktop: false,
  desktopUp: false,
  onlyMediumDesktop: false,
  mediumDesktopUp: false,
  largeDesktop: false,
});

export const useResponsiveContext = () => {
  const context = useContext(ResponsiveContext);
  if (context === undefined) {
    throw new Error(
      'useResponsiveContext must be used within a ResponsiveProvider'
    );
  }
  return context;
};

export const ResponsiveProvider: React.FC = ({ children }) => {
  const [responsiveState, setResponsiveState] = useState<ResponsiveBreakPoint>({
    onlyMobile: false,
    onlyTablet: false,
    tabletUp: false,
    onlyTabletLS: false,
    tabletLSUp: false,
    onlyDesktop: false,
    desktopUp: false,
    onlyMediumDesktop: false,
    mediumDesktopUp: false,
    largeDesktop: false,
  });
  const mobileMatch = useMediaQuery(respondTo.onlyMobile);
  const tabletMatch = useMediaQuery(respondTo.tabletUp);
  const tabletLSMatch = useMediaQuery(respondTo.tabletLSUp);
  const desktopMatch = useMediaQuery(respondTo.desktopUp);
  const mediumDesktopMatch = useMediaQuery(respondTo.mediumDesktopUp);
  const largeDesktopMatch = useMediaQuery(respondTo.largeDesktopUp);
  useEffect(() => {
    const responsiveMatch: ResponsiveBreakPoint = {
      onlyMobile: mobileMatch,
      onlyTablet: !tabletLSMatch && tabletMatch,
      tabletUp: tabletMatch,
      onlyTabletLS: !desktopMatch && tabletLSMatch,
      tabletLSUp: tabletLSMatch,
      onlyDesktop: !mediumDesktopMatch && desktopMatch,
      desktopUp: desktopMatch,
      onlyMediumDesktop: !largeDesktopMatch && mediumDesktopMatch,
      mediumDesktopUp: mediumDesktopMatch,
      largeDesktop: largeDesktopMatch,
    };
    setResponsiveState(responsiveMatch);
  }, [
    mobileMatch,
    tabletMatch,
    tabletLSMatch,
    desktopMatch,
    mediumDesktopMatch,
    largeDesktopMatch,
  ]);
  return (
    <ResponsiveContext.Provider value={responsiveState}>
      {children}
    </ResponsiveContext.Provider>
  );
};
