import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { AppContext } from '../../contexts/AppContext';
import { Typography, VerticalLayout } from '../ui';
import { OptionContainer } from '../general';
import spacing from '../ui/style/spacing';
import palette from '../ui/style/palette';
import { Ratings } from './wizard.consts';
import { FormFieldType } from './wizard.types';
import WellbeingCard from './WellbeingCard';
import { HeadingStyle, QuestionStyle } from './styles';

type WellbeingStepProps = {
  setFieldValue: (field: string, value: FormFieldType) => void;
  value: FormFieldType;
  field: string;
};

const WellbeingStep: React.FC<WellbeingStepProps> = ({
  setFieldValue,
  value,
  field,
}) => {
  const { state } = useContext(AppContext);
  const { t } = useTranslation();

  const handleSelect = (selectedValue: number) => {
    setFieldValue(field, selectedValue);
  };

  return (
    <VerticalLayout responsive data-testid="wb-WellbeingStep">
      <HeadingStyle>
        <Typography responsive size="xxLarge" color={palette.purple.p4}>
          Hi {state.studentProfile.firstName}
        </Typography>
      </HeadingStyle>
      <VerticalLayout responsive align="center" childGap={spacing.xLarge}>
        <QuestionStyle marginBottom={spacing.smallPlus}>
          <Typography weight="bold" align="center" size="xxLarge" responsive>
            {t('How have you been feeling this week?')}
          </Typography>
        </QuestionStyle>
        <OptionContainer>
          {Ratings.map(rating => (
            <WellbeingCard
              key={rating.value}
              onSelect={handleSelect}
              rating={rating}
              value={value}
            />
          ))}
        </OptionContainer>
      </VerticalLayout>
    </VerticalLayout>
  );
};

export default WellbeingStep;
