import 'react-toastify/dist/ReactToastify.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import styled, { ThemeProvider } from 'styled-components/macro';
import { ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles';
import { ToastContainer } from 'react-toastify';
import { CssBaseline } from '@material-ui/core';
import font from './components/ui/style/font';
import palette from './components/ui/style/palette';
import {
  Home,
  Login,
  Onboarding,
  Survey,
  ThankYou,
  ForgotPassword,
  NotFound,
  Support,
  OnboardingHelp,
  TermsOfUse,
  PrivacyPolicy,
} from './pages';
import { ProtectedRoute } from './components/general';
import { AppProvider } from './contexts/AppContext';
import { ResponsiveProvider } from './contexts/ResponsiveContext';
import theme from './theme';

const Container = styled.div`
  font-family: ${font.family.body};
  height: 100%;
  & > div {
    height: 100%;
  }
`;

const Wrapper = styled.div`
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
`;

const App: React.FC = () => {
  return (
    <MuiThemeProvider theme={theme}>
      <ThemeProvider theme={{ palette }}>
        <ResponsiveProvider>
          <CssBaseline />
          <Container>
            <AppProvider>
              <Wrapper>
                <Router>
                  <Switch>
                    <ProtectedRoute exact path="/">
                      <Home />
                    </ProtectedRoute>
                    <Route path="/login/:cycleId?">
                      <Login />
                    </Route>
                    <Route path="/legal/terms-of-use">
                      <TermsOfUse />
                    </Route>
                    <Route path="/legal/privacy-policy">
                      <PrivacyPolicy />
                    </Route>
                    <Route path="/onboarding">
                      <Onboarding />
                    </Route>
                    <ProtectedRoute path="/survey">
                      <Survey />
                    </ProtectedRoute>
                    <ProtectedRoute path="/thank-you">
                      <ThankYou />
                    </ProtectedRoute>
                    <Route path="/forgot-password">
                      <ForgotPassword />
                    </Route>
                    <ProtectedRoute path="/support-1">
                      <Support supportType="struggling" />
                    </ProtectedRoute>
                    <ProtectedRoute path="/support-2">
                      <Support supportType="dont-want-answer" />
                    </ProtectedRoute>
                    <ProtectedRoute path="/onboarding-tips">
                      <OnboardingHelp type="tips" />
                    </ProtectedRoute>
                    <ProtectedRoute path="/help">
                      <OnboardingHelp type="helps" />
                    </ProtectedRoute>
                    <Route path="*">
                      <NotFound />
                    </Route>
                  </Switch>
                </Router>
              </Wrapper>
            </AppProvider>
            <ToastContainer />
          </Container>
        </ResponsiveProvider>
      </ThemeProvider>
    </MuiThemeProvider>
  );
};

export default App;
