import styled, { css } from 'styled-components/macro';
import { respondTo } from '../../ui/style/dimensions';

type ContainerProps = {
  color: string;
  borderColor: string;
  bgColor: string;
  bgActiveColor: string;
  shadowColor?: string;
  active?: boolean;
};

type ComponentsProps = {
  marginTop?: number;
  marginBottom?: number;
};

type StyledProps = {
  theme: ComponentsProps;
};

const Root = styled.div<StyledProps>`
  display: flex;
  flex-direction: column;
  width: 100%;
  ${respondTo.tabletLSUp} {
    width: calc(100% - 16px);
  }
  ${respondTo.desktopUp} {
    flex-direction: row;
    justify-content: flex-start;
  }
`;

const OptionContainer: React.FC<ComponentsProps> = ({
  children,
  marginTop = 0,
  marginBottom = 0,
  ...rest
}) => {
  return (
    <Root {...rest} theme={{ marginBottom, marginTop }}>
      {children}
    </Root>
  );
};

export default OptionContainer;

export const StyledContainer = styled.div<ContainerProps>`
  border-radius: 16px;
  border-width: 4px;
  border-style: solid;
  ${props =>
    props.color &&
    css`
      color: ${props.color};
    `}
  border-color: ${props => props.borderColor};
  background-color: ${props =>
    props.active ? props.bgActiveColor : props.bgColor ?? '#fff'};
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  width: 100%;
  height: 68px;
  align-items: center;
  ${respondTo.desktopUp} {
    padding: 0;
    width: 222px;
    height: 183px;
    margin-bottom: 0;
    flex-direction: column;
    justify-content: center;
    :hover {
      box-shadow: 0px 32px 50px -11px ${props => props.shadowColor};
    }
  }
`;

export const StyledImageContainer = styled.div`
  margin-left: 24px;
  ${respondTo.desktopUp} {
    margin-left: 0;
    margin-top: 48px;
  }
`;
export const StyledImage = styled.img`
  width: 35px;
  ${respondTo.desktopUp} {
    width: 150px;
  }
`;

export const StyledLabelContainer = styled.div`
  margin-left: 24px;
  ${respondTo.desktopUp} {
    margin-left: 0;
  }
`;
