import HorizontalLayout from '../HorizontalLayout';
import VerticalLayout from '../VerticalLayout';
import Clickable from '../Clickable';

type Direction = 'column' | 'row';

type IconProps = {
  alt: string;
  dir?: Direction;
  icon: string;
  text?: string;
  childGap?: number;
  onClick: () => void;
  className?: string;
};

const Icon: React.FC<IconProps> = ({
  alt,
  icon,
  dir = 'row',
  onClick,
  childGap = 0,
  className,
  children,
}) => {
  let IconLayout = HorizontalLayout;
  if (dir === 'column') {
    IconLayout = VerticalLayout;
  }
  return (
    <Clickable onClick={onClick} className={className}>
      <IconLayout childGap={childGap}>
        <img src={icon} alt={alt} />
        {children}
      </IconLayout>
    </Clickable>
  );
};

export default Icon;
