import styled from 'styled-components/macro';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useCallback } from 'react';
import { Layout } from '../components/general';
import {
  Button,
  VerticalLayout,
  HorizontalLayout,
  Clickable,
  Typography,
} from '../components/ui';

import palette from '../components/ui/style/palette';
import OnBoarding1 from '../assets/icons/onboarding-icon1.svg';
import OnBoarding2 from '../assets/icons/onboarding-icon2.svg';
import OnBoarding3 from '../assets/icons/onboarding-icon3.svg';
import { respondTo } from '../components/ui/style/dimensions';

type OnboardingProps = {
  //
};

const OnBoardingLayout = styled(VerticalLayout)`
  min-height: calc(100vh - 144px);
  ${respondTo.tabletUp} {
    width: 450px;
  }
  ${respondTo.tabletLSUp} {
    width: 920px;
  }
  ${respondTo.desktopUp} {
    width: 1200px;
  }
`;

const OnBoardingBox = styled(Clickable)`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: ${palette.white};
  margin: 0 10px 24px;
  border-radius: 26px;
  box-shadow: 0px 4px 10px rgba(153, 159, 173, 0.2);

  cursor: pointer;
  transition: all 0.35s;
  padding: 20px;
  &:hover {
    box-shadow: 10px 18px 10px rgb(153 159 173 / 20%);
  }
  aspect-ratio: 1/1;
  min-height: 322px;
  ${respondTo.tabletLSUp} {
    flex-basic: 400px;
  }
`;
const OnBoardingContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1;
  padding-top: 20px;
  ${respondTo.tabletLSUp} {
    flex-direction: row;
    padding-top: 0;
  }
`;
const OnBoardingImage = styled.img`
  margin-bottom: 20px;
  ${respondTo.tabletLSUp} {
    margin-bottom: 40px;
  }
`;

const SurveyButton = styled(Button)`
  ${respondTo.tabletLSUp} {
    min-width: 400px;
  }
`;

const Onboarding: React.FC<OnboardingProps> = () => {
  const { t } = useTranslation();

  const history = useHistory();

  const handleNextButton = () => {
    history.push('/survey');
  };

  const goToPage = useCallback(
    (page = '/') => {
      history.push(page);
    },
    [history]
  );

  return (
    <Layout color="light">
      <OnBoardingLayout>
        <OnBoardingContainer>
          <OnBoardingBox onClick={() => goToPage('/onboarding-why')}>
            <OnBoardingImage src={OnBoarding1} />
            <Typography
              size="mediumPlus"
              align="center"
              color={palette.purple.p4}
            >
              {t('Why does wellbeing matter?')}
            </Typography>
          </OnBoardingBox>
          <OnBoardingBox
            title={t('Onboarding tips')}
            onClick={() => goToPage('/onboarding-tips')}
          >
            <OnBoardingImage src={OnBoarding2} />
            <Typography
              size="mediumPlus"
              align="center"
              color={palette.purple.p4}
            >
              {t('Tips for your wellbeing check-in')}
            </Typography>
          </OnBoardingBox>
          <OnBoardingBox onClick={() => goToPage('/help')}>
            <OnBoardingImage src={OnBoarding3} />
            <Typography
              size="mediumPlus"
              align="center"
              color={palette.purple.p4}
            >
              {t('How to ask for help')}
            </Typography>
          </OnBoardingBox>
        </OnBoardingContainer>
        <HorizontalLayout justify="center">
          <SurveyButton responsive onClick={handleNextButton}>
            {t('Start survey')}
          </SurveyButton>
        </HorizontalLayout>
      </OnBoardingLayout>
    </Layout>
  );
};

export default Onboarding;
