import { useTranslation } from 'react-i18next';
import styled, { css } from 'styled-components/macro';
import { useHistory } from 'react-router-dom';
import { useContext } from 'react';
import { Layout } from '../components/general';
import { Typography, VerticalLayout, Button, Icon } from '../components/ui';
import spacing from '../components/ui/style/spacing';
import platypusIcon from '../assets/icons/platypus.svg';

import Logger from '../services/logger';
import { respondTo } from '../components/ui/style/dimensions';
import { px2em } from '../util/helpers';
import { useResponsiveContext } from '../contexts/ResponsiveContext';
import { clearLocalStorage } from '../services/storage.service';
import { AppContext } from '../contexts/AppContext';

type MarginProps = {
  marginBottom?: number;
  marginTop?: number;
};

const TitleStyle = styled.div<MarginProps>`
  padding: 0 25px;
  ${respondTo.tabletLSUp} {
    padding: 0 75px;
  }
  ${props =>
    props.marginTop &&
    css`
      margin-top: ${px2em(props.marginTop)};
    `}
  ${props =>
    props.marginBottom &&
    css`
      margin-bottom: ${px2em(props.marginBottom)};
      width: ;
    `}
`;

const IconDiv = styled.div`
  padding: 40px 0 90px;
  ${respondTo.desktopUp} {
    padding: 50px 0 120px;
  }
  img {
    max-width: calc(100% - 135px);
    margin: 0 auto;
    ${respondTo.desktopUp} {
      max-width: calc(100% - 100px);
    }
  }
`;

const ThankYou: React.FC = () => {
  const { t } = useTranslation();
  const { state } = useContext(AppContext);
  const responsive = useResponsiveContext();
  const history = useHistory();

  const handleLogout = async () => {
    clearLocalStorage();
    history.replace(`/login/${state.cycle}`);
  };

  return (
    <Layout>
      <VerticalLayout align="center" childGap={spacing.xxLarge} responsive>
        <TitleStyle marginBottom={spacing.xLarge} marginTop={spacing.xxLarge}>
          <Typography
            weight="bold"
            align="center"
            size={responsive.onlyMobile ? 'large' : 'xLarge'}
            responsive
          >
            {t('Youre finished')}
          </Typography>
        </TitleStyle>
        <VerticalLayout align="center" responsive>
          {!state.metaData.isBaseline && (
            <IconDiv>
              <Icon
                icon={platypusIcon}
                alt={t('Finish')}
                onClick={() => Logger.info('implement me')}
              />
            </IconDiv>
          )}
          <Button responsive variant="secondary" onClick={handleLogout}>
            {t('Logout')}
          </Button>
        </VerticalLayout>
      </VerticalLayout>
    </Layout>
  );
};

export default ThankYou;
