import { useContext, useCallback, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components/macro';
import SurveyWizard from '../components/SurveyWizard';
import { Layout } from '../components/general';
import { Typography, VerticalLayout, Icon } from '../components/ui';
import { AppContext } from '../contexts/AppContext';
import WellbeingProgressBar from '../components/SurveyWizard/WellbeingProgressBar';
import QuestionIconColor from '../assets/icons/question-color.svg';
import spacing from '../components/ui/style/spacing';
import BaselineSurvey from '../components/SurveyWizard/BaselineSurvey';
import {
  LocalStorage,
  setLocalStorage,
  getLocalStorage,
} from '../services/storage.service';
import { useResponsiveContext } from '../contexts/ResponsiveContext';

const Body = styled(VerticalLayout)`
  height: calc(100vh - 200px);
`;

const Survey: React.FC = () => {
  const { state } = useContext(AppContext);
  const hasQuestion = Object.keys(state.questions).length > 0;
  const [currentStep, setCurrentStep] = useState<number>(
    parseInt(getLocalStorage(LocalStorage.CURRENT_STEP, '0'), 10)
  );

  const { t } = useTranslation();
  const history = useHistory();
  const responsiveContext = useResponsiveContext();

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  const onChangeQuestion = useCallback(
    (step: number) => {
      setLocalStorage(LocalStorage.CURRENT_STEP, step.toString());
      setCurrentStep(step);
      if (responsiveContext.onlyMobile) {
        scrollToTop();
      }
    },
    [responsiveContext]
  );

  const onHelpClick = useCallback(() => {
    history.push('/help');
  }, [history]);

  const onChangeStep = (nextStep: number) => {
    setLocalStorage(LocalStorage.CURRENT_STEP, nextStep.toString());
    setCurrentStep(nextStep);
  };

  if (
    !state.isAnswered &&
    hasQuestion &&
    state.metaData.cycleId &&
    state.metaData.iterationId
  ) {
    return (
      <Layout responsive full>
        {!state.metaData.isBaseline && (
          <WellbeingProgressBar
            currentStep={currentStep + 1}
            onChangeStep={onChangeStep}
          />
        )}
        {state.metaData.isBaseline ? (
          <BaselineSurvey wizard={state.questions} />
        ) : (
          <SurveyWizard
            onChange={onChangeQuestion}
            wizard={state.questions}
            currentStep={currentStep}
          />
        )}
      </Layout>
    );
  }

  return (
    <Layout>
      <Body align="center" justify="center">
        <Typography size="xxLarge" color="gray">
          {t('Hello')} {state.studentProfile.firstName},
        </Typography>
        <Typography size="xxLarge" color="gray">
          {t('Your survey is not available yet.')}
        </Typography>
        <Typography size="xxLarge" color="gray">
          {t(
            'Your teacher will be able to tell you when the survey opens again.'
          )}
        </Typography>
        <Typography size="xxLarge" color="gray">
          {t('If you would like some help with your wellbeing, click the')}{' '}
          <Icon
            onClick={onHelpClick}
            icon={QuestionIconColor}
            alt={t('Help')}
            childGap={spacing.smallPlus}
          />
        </Typography>
      </Body>
    </Layout>
  );
};

export default Survey;
