import styled, { css } from 'styled-components/macro';
import CircularProgress from '@material-ui/core/CircularProgress';
import { makeStyles } from '@material-ui/core';
import Typography from '../Typography';
import palette from '../style/palette';
import spacing from '../style/spacing';
import font from '../style/font';
import { respondTo } from '../style/dimensions';

type ButtonVariant = 'primary' | 'secondary' | 'outlined' | 'link';

type ButtonProps = {
  onClick: () => void;
  variant?: ButtonVariant;
  disabled?: boolean;
  responsive?: boolean;
  loading?: boolean;
  className?: string;
};

type StyledProps = {
  responsive?: boolean;
  disabled?: boolean;
};

const BaseButton = styled.button<StyledProps>`
  position: relative;
  border-radius: ${spacing.medium}px;
  padding: 4px 25px;
  height: 64px;
  border: none;
  text-decoration: none;
  color: ${palette.white};
  cursor: pointer;
  ${props =>
    props.responsive &&
    css`
      width: 100%;
    `}
  ${props =>
    props.disabled &&
    css`
      cursor: default;
      pointer-event: none;
      color: rgba(0, 0, 0, 0.26) !important;
      background-color: rgba(0, 0, 0, 0.12) !important;
      &:hover {
        background-color: rgba(0, 0, 0, 0.12) !important;
      }
    `}
  ${respondTo.tabletLSUp} {
    width: 288px;
  }
`;

const PrimaryButton = styled(BaseButton)`
  background: ${palette.purple.p1};
  :hover {
    background: ${palette.purple.p2};
  }
`;

const SecondaryButton = styled(BaseButton)`
  background: ${palette.purple.p6};
  color: ${palette.purple.p1};
  :hover {
    background: ${palette.purple.p7};
  }
`;

const OutlinedButton = styled(BaseButton)`
  background: ${palette.white};
  border: 1px solid ${palette.purple.p1};
  color: ${palette.purple.p1};
  :hover {
    background: ${palette.purple.p7};
  }
`;

const LinkButton = styled(BaseButton)`
  background: transparent;
  border: 0;
  color: ${palette.purple.p1};
  ${respondTo.tabletUp} {
    width: auto;
  }
`;

const useStyles = makeStyles(() => ({
  buttonProgress: {
    color: palette.purple.p4,
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -18,
    marginLeft: -18,
  },
}));

const Button: React.FC<ButtonProps> = ({
  onClick,
  children,
  variant = 'primary',
  className,
  disabled,
  loading,
  responsive,
}) => {
  const classes = useStyles();
  const getButtonVariant = () => {
    switch (variant) {
      case 'primary':
        return PrimaryButton;
      case 'secondary':
        return SecondaryButton;
      case 'outlined':
        return OutlinedButton;
      case 'link':
        return LinkButton;
      default:
        return PrimaryButton;
    }
  };

  const ButtonComponent = getButtonVariant();
  return (
    <ButtonComponent
      data-testid="app-button"
      onClick={onClick}
      type="button"
      disabled={disabled || loading}
      className={className}
      responsive={responsive}
    >
      <Typography
        size="mediumLg"
        align="center"
        textDecoration={variant === 'link' ? 'underline' : 'none'}
      >
        {children}
      </Typography>
      {loading && (
        <CircularProgress
          size={font.size.xLarge}
          className={classes.buttonProgress}
        />
      )}
    </ButtonComponent>
  );
};

export default Button;
