import InputBase from '@material-ui/core/InputBase';
import styled, { css } from 'styled-components/macro';
import palette from '../style/palette';
import { respondTo } from '../style/dimensions';
import spacing from '../style/spacing';
import Typography from '../Typography';

type TextFieldProps = {
  required?: boolean;
  fullWidth?: boolean;
  name?: string;
  id?: string;
  placeholder?: string;
  onChange: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement>;
  onBlur?: React.FocusEventHandler<HTMLInputElement | HTMLTextAreaElement>;
  error?: boolean;
  disabled?: boolean;
  helperText?: string;
};

const StyledTextField = styled(InputBase)<TextFieldProps>`
  border-radius: ${spacing.medium}px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  height: 64px;
  width: 327px;
  padding: ${spacing.mediumPlus}px ${spacing.xxLarge}px;
  &:hover {
    box-sizing: border-box;
    border: 2px solid ${palette.purple.p2};
    color: ${palette.purple.p2};
  }
  &:focus-within {
    box-sizing: border-box;
    border: 2px solid ${palette.purple.p1};
  }
  ${respondTo.tabletUp} {
    width: 496px;
  }
  ${respondTo.desktopUp} {
    width: 440px;
  }
  ${props =>
    props.error &&
    css`
      box-sizing: border-box;
      border: 2px solid ${palette.red.r3};
      color: ${palette.red.r3};
    `}
`;

const TextField: React.FC<TextFieldProps> = ({
  required,
  fullWidth,
  id,
  name,
  placeholder,
  onChange,
  onBlur,
  error,
  disabled,
  helperText,
}) => {
  return (
    <div>
      <StyledTextField
        required={required}
        id={id}
        name={name}
        fullWidth={fullWidth}
        placeholder={placeholder}
        onChange={onChange}
        onBlur={onBlur}
        error={error}
        disabled={disabled}
      />
      {helperText && (
        <Typography color={palette.red.r3}>{helperText}</Typography>
      )}
    </div>
  );
};

export default TextField;
