let id = 0;
/**
 * Helper function used to generate id with prefix. To be used when rendering in a loop
 * @param prefix the prefix to be used. Default is id
 * @returns generated id in format prefix_number
 */
export const makeId = (prefix = 'id') => {
  id += 1;
  return `${prefix}_${id}`;
};

/**
 * App configuration constant
 */
export const Config = {
  API_URL: process.env.REACT_APP_BASE_API_URL,
};

/**
 * Helper function used to convert px to em
 * @param pixel the pixel to be used
 * @param context the context to be used. Detault is 16px
 */

export const px2em = (pixel: string | number, context = 16) => {
  if (typeof pixel === 'string') {
    return `${parseInt(pixel, 10) / context}em`;
  }
  return `${pixel / context}em`;
};
