import { Clickable, Typography } from '../../ui';
import palette from '../../ui/style/palette';
import { FormFieldType } from '../wizard.types';
import {
  StyledContainer,
  StyledImage,
  StyledImageContainer,
  StyledLabelContainer,
} from './styles';

type RatingType = {
  value: number;
  label: string;
  icon: string;
};

type WellbeingCardProps = {
  onSelect: (number: number) => void;
  rating: RatingType;
  value: FormFieldType;
};

const WellbeingCard: React.FC<WellbeingCardProps> = ({
  rating,
  value,
  onSelect,
}) => {
  const getColors = (number: number) => {
    switch (number) {
      case 1:
        return palette.red.r1;
      case 2:
        return palette.yellow.y2;
      case 3:
        return palette.yellow.y1;
      case 4:
        return palette.green.g1;
      case 5:
        return palette.green.g2;
      default:
        return palette.white;
    }
  };
  return (
    <Clickable onClick={() => onSelect(rating.value)}>
      <StyledContainer
        data-testid="wb-WellbeingCard"
        color={getColors(rating.value)}
        active={rating.value === value}
      >
        <StyledImageContainer>
          <StyledImage src={rating.icon} alt={rating.label} />
        </StyledImageContainer>
        <StyledLabelContainer>
          <Typography align="center" size="large">
            {rating.label}
          </Typography>
        </StyledLabelContainer>
      </StyledContainer>
    </Clickable>
  );
};

export default WellbeingCard;
