import { useTranslation } from 'react-i18next';

import styled from 'styled-components/macro';
import { useState } from 'react';
import { Clickable, HorizontalLayout, Typography, VerticalLayout } from '../ui';
import spacing from '../ui/style/spacing';
import WellbeingStep from './WellbeingStep';
import { FormFieldType, Step } from './wizard.types';
import BehaviourQuestionStep from './BehaviourQuestionStep';
import Card from '../general/Card';
import { StyledLabelContainer, StyledContainer } from '../general/Card/styles';

import { FieldTypesConst } from './wizard.consts';
import { QuestionStyle, StyledExplanationQuestion } from './styles';
import palette from '../ui/style/palette';
import { OptionContainer } from '../general';
import { respondTo } from '../ui/style/dimensions';
import font from '../ui/style/font';
import InfoIcon from '../../assets/icons/info.svg';
import TransitionsModal from '../ui/Modal';

type WizardStepProps = {
  shouldRender: boolean;
  step: Step;
  setFieldValue: (field: string, value: FormFieldType) => void;
  value: FormFieldType;
};

const StyledLabelContainerStyle1 = styled(StyledLabelContainer)`
  font-size: 20px;
  ${respondTo.desktopUp} {
    font-size: ${font.size.large};
    font-family: 'CircularStd-Bold';
  }
`;

const StyledContainerStyle1 = styled(StyledContainer)`
  :hover,
  &.active {
    color: ${palette.white};
    background-color: ${palette.purple.p4};
  }
`;

const StyledQuestionLabel = styled(Typography)`
  display: flex;
  align-items: center;
  img {
    margin-left: 5px;
    cursor: pointer;
    width: 22px;
    ${respondTo.tabletLSUp} {
      width: 26px;
    }
    ${respondTo.desktopUp} {
      width: 30px;
    }
  }
`;
const WizardStep: React.FC<WizardStepProps> = ({
  shouldRender,
  step,
  setFieldValue,
  value,
}) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  if (!shouldRender) return null;
  const renderRadioFields = () => {
    return (
      <VerticalLayout childGap={spacing.mediumPlus} data-testid="radio-field">
        <HorizontalLayout justify="center">
          <OptionContainer>
            {step.fields.map(field => (
              <Card
                key={`id-${field.label}`}
                styles={{
                  color: palette.black.bl2,
                  shadowColor: palette.purple.p4,
                  bgColor: palette.white,
                  borderColor: palette.purple.p4,
                  bgActiveColor: palette.purple.p4,
                  labelContainer: StyledLabelContainerStyle1,
                  container: StyledContainerStyle1,
                }}
                data={{ value: field.value, label: field.label }}
                value={value}
                onSelect={() => setFieldValue(step.id, field.value)}
              />
            ))}
          </OptionContainer>
        </HorizontalLayout>
      </VerticalLayout>
    );
  };

  const renderStepField = () => {
    switch (step.fieldsType) {
      case FieldTypesConst.WELL_BEING:
        return (
          <WellbeingStep
            setFieldValue={setFieldValue}
            field={step.id}
            value={value}
          />
        );
      case FieldTypesConst.RADIO_BUTTON:
        return renderRadioFields();
      case FieldTypesConst.BEHAVIOUR:
        return (
          <BehaviourQuestionStep setFieldValue={setFieldValue} value={value} />
        );
      default:
        return <div>Invalid form field</div>;
    }
  };

  const renderQuestion = () => {
    if (step.isCustom) return null;
    return (
      <QuestionStyle
        marginBottom={spacing.xxLarge}
        marginTop={spacing.xxxLarge}
      >
        <Typography
          weight="normal"
          size="largePlus"
          color={palette.blue.b2}
          align="center"
          responsive
        >
          {t('How often is the following statement true for you?')}
        </Typography>
        <StyledQuestionLabel
          weight="bold"
          color={palette.purple.p4}
          size="xxLarge"
          align="center"
          responsive
        >
          {t(step.question)}
          {step.explanation && (
            <Clickable onClick={() => setOpen(true)}>
              <img alt="" src={InfoIcon} />
            </Clickable>
          )}
        </StyledQuestionLabel>
      </QuestionStyle>
    );
  };

  return (
    <VerticalLayout
      childGap={spacing.xxLarge}
      align="center"
      responsive
      data-testid="wizard-step"
    >
      {renderQuestion()}
      {renderStepField()}
      {step.explanation && (
        <TransitionsModal showClose isOpen={open} setOpen={setOpen}>
          <StyledExplanationQuestion size="mediumLg" color={palette.purple.p4}>
            {step.explanation}
          </StyledExplanationQuestion>
        </TransitionsModal>
      )}
    </VerticalLayout>
  );
};

export default WizardStep;
