import {
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Radio,
  RadioGroup,
} from '@material-ui/core';
import { FormikValues, useFormik } from 'formik';
import { useCallback, useContext, useState } from 'react';

import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import styled from 'styled-components/macro';
import * as Yup from 'yup';
import { AppContext } from '../../contexts/AppContext';
import { saveResponse } from '../../services/api.service';
import { CreateSurveyResponse, Survey } from '../../services/api.types';
import InfoIcon from '../../assets/icons/info.svg';
import {
  ActivityIndicator,
  Button,
  HorizontalLayout,
  VerticalLayout,
  Typography,
  Clickable,
} from '../ui';
import TransitionsModal from '../ui/Modal';
import { breakpoints, respondTo } from '../ui/style/dimensions';
import font from '../ui/style/font';
import palette from '../ui/style/palette';
import spacing from '../ui/style/spacing';
import { Step, WizardType } from './wizard.types';
import { StyledExplanationQuestion } from './styles';

const BaselineSurveyContainer = styled(VerticalLayout)`
  max-width: ${breakpoints.desktopWidth}px;
  padding: ${spacing.mediumPlus}px;
  ${respondTo.desktopUp} {
    padding: ${spacing.xLarge}px;
  }
  .heading-username {
    margin-bottom: ${spacing.smallPlus}px;
    ${respondTo.onlyMobile} {
      font-size: ${font.size.large};
    }
  }
  .heading-notice {
    margin-bottom: ${spacing.small}px;
    ${respondTo.onlyMobile} {
      font-size: ${font.size.mediumLg};
    }
  }
  .heading-question {
    ${respondTo.onlyMobile} {
      font-size: ${font.size.mediumLg};
      line-height: 1.2;
    }
  }
  .MuiFormLabel-root {
    font-size: ${font.size.mediumLg};
    margin-bottom: ${spacing.medium}px;
    span {
      margin-right: ${spacing.small}px;
    }
    ${respondTo.desktopUp} {
      font-size: calc(${font.size.large} + 3px);
    }
  }
  .MuiFormHelperText-root.Mui-error {
    font-size: ${font.size.medium};
  }

  .MuiFormGroup-root {
    padding-left: ${spacing.small}px;
    ${respondTo.tabletUp} {
      padding-left: ${spacing.xxLarge}px;
    }
  }
  .extra-errors {
    margin-top: ${spacing.large}px;
    font-size: ${font.size.mediumPlus};
    ${respondTo.tabletUp} {
      font-size: ${font.size.large};
    }
  }
`;
const QuestionContainer = styled.div`
  margin-top: ${spacing.large}px;
  ${respondTo.desktopUp} {
    margin-top: ${spacing.xLarge}px;
  }
`;

// eslint-disable-next-line
const StyledFormLabel: any = styled(FormLabel)`
  display: flex;
  align-items: center;
  font-weight: ${font.weight.bold};
  img {
    margin-left: 5px;
    cursor: pointer;
    width: 22px;
    ${respondTo.tabletLSUp} {
      width: 24px;
    }
    ${respondTo.desktopUp} {
      width: 26px;
    }
  }
`;
type BaselineSurveyProps = {
  wizard: WizardType;
};

const BaselineSurvey: React.FC<BaselineSurveyProps> = ({ wizard }) => {
  const { state } = useContext(AppContext);
  const [currentExplanation, setExplanation] = useState('');
  const [isSubmitForm, setSubmitForm] = useState(false);
  const [open, setOpen] = useState(false);
  const history = useHistory();
  const { t } = useTranslation();
  const { steps } = wizard;
  /**
   * Get wizard initial values
   * @returns form initial values
   */
  const getInitialValues = () => {
    // eslint-disable-next-line
    return {} as any;
  };
  const getValidationSchema = () => {
    // eslint-disable-next-line
    const validationSchema = {} as any;
    steps.forEach(step => {
      validationSchema[step.id] = Yup.string().required(
        'Please answer the question'
      );
    });
    return validationSchema;
  };
  const formik = useFormik({
    initialValues: getInitialValues(),
    validationSchema: Yup.object().shape(getValidationSchema()),
    onSubmit: async (values: FormikValues) => {
      const surveys: Survey[] = [];
      const mutatedValues = { ...values };
      Object.keys(mutatedValues).forEach(key => {
        const step = steps.find(s => s.id === key) || ({} as Step);
        surveys.push({
          id: step.id,
          type: step.type,
          response: mutatedValues[key],
          question: step.question,
        });
      });

      try {
        const student = state.studentProfile;
        delete student.createdAt;
        delete student.onboardingComplete;
        const apiPayload = {
          student,
          metadata: {
            ...state.metaData,
          },
          skip: false,
          answers: surveys,
        } as CreateSurveyResponse;
        await saveResponse(apiPayload);
        formik.resetForm();
        history.push('/thank-you');
      } catch (e) {
        formik.setSubmitting(false);
        toast.error(t('Error saving your response please try again'));
      }
    },
  });

  const renderQuestions = useCallback(() => {
    return steps.map(step => {
      return (
        <QuestionContainer key={step.id}>
          <FormControl
            key={step.id}
            component="fieldset"
            error={!!formik.errors[step.id] && isSubmitForm}
          >
            <StyledFormLabel component="legend">
              {step.question}
              {step.explanation && (
                <Clickable
                  onClick={() => {
                    setExplanation(step.explanation);
                    setOpen(true);
                  }}
                >
                  <img alt="" src={InfoIcon} />
                </Clickable>
              )}
            </StyledFormLabel>
            <RadioGroup
              value={formik.values[step.id] || null}
              name={step.id}
              onChange={event =>
                formik.setFieldValue(step.id, event.target.value)
              }
            >
              <FormControlLabel value="1" control={<Radio />} label="Never" />
              <FormControlLabel value="2" control={<Radio />} label="Rarely" />
              <FormControlLabel
                value="3"
                control={<Radio />}
                label="Sometimes"
              />
              <FormControlLabel value="4" control={<Radio />} label="Often" />
              <FormControlLabel
                value="5"
                control={<Radio />}
                label="Almost always"
              />
              {isSubmitForm && (
                <FormHelperText>{formik.errors[step.id]}</FormHelperText>
              )}
            </RadioGroup>
          </FormControl>
        </QuestionContainer>
      );
    });
  }, [steps, formik, isSubmitForm]);
  const handleSubmit = () => {
    setSubmitForm(true);
    formik.handleSubmit();
  };
  return (
    <HorizontalLayout justify="center">
      <BaselineSurveyContainer>
        <Typography
          weight="normal"
          size="largePlus"
          color={palette.blue.b2}
          className="heading-username"
        >
          {t('Hi')} {state.studentProfile.firstName}
        </Typography>
        <Typography
          weight="normal"
          size="large"
          color={palette.blue.b2}
          className="heading-notice"
        >
          {t('Please answer all questions')}
        </Typography>

        <Typography
          weight="normal"
          size="large"
          color={palette.purple.p4}
          className="heading-question"
        >
          {t('How often is the following statement true for you?')}
        </Typography>

        <VerticalLayout>{renderQuestions()}</VerticalLayout>
        <VerticalLayout>
          {isSubmitForm && Object.keys(formik.errors).length > 0 && (
            <Typography
              weight="normal"
              className="extra-errors"
              size="large"
              color={palette.red.r1}
            >
              {t(
                'It looks like you might have not answered some questions. Please scroll up and check your answers'
              )}
            </Typography>
          )}
          <HorizontalLayout
            justify="flex-end"
            childGap={spacing.medium}
            marginTop={spacing.xxLarge}
          >
            <ActivityIndicator active={formik.isSubmitting}>
              <Button responsive onClick={handleSubmit}>
                {t('Submit')}
              </Button>
            </ActivityIndicator>
          </HorizontalLayout>
        </VerticalLayout>
      </BaselineSurveyContainer>
      <TransitionsModal showClose isOpen={open} setOpen={setOpen}>
        <StyledExplanationQuestion size="mediumLg" color={palette.purple.p4}>
          {currentExplanation}
        </StyledExplanationQuestion>
      </TransitionsModal>
    </HorizontalLayout>
  );
};

export default BaselineSurvey;
