import classNames from 'classnames';
import styled from 'styled-components/macro';

type ComponentsProps = {
  inline?: boolean;
  align?: string;
  justify?: string;
  childGap?: number;
  marginTop?: number;
  marginBottom?: number;
  marginLeft?: number;
  marginRight?: number;
  className?: string;
};

type StyledProps = {
  theme: ComponentsProps;
};

const Root = styled.div<StyledProps>`
  display: ${props => (props.theme.inline ? 'inline-flex' : 'flex')};
  width: ${props => (props.theme.inline ? 'auto' : '100%')};
  align-items: ${props => props.theme.align};
  justify-content: ${props => props.theme.justify};
  color: inherit;
  margin: ${props => props.theme.marginTop}px
    ${props => props.theme.marginRight}px ${props => props.theme.marginBottom}px
    ${props => props.theme.marginLeft}px;

  &.hasChildren > * + * {
    margin-left: ${props => props.theme.childGap}px;
  }
`;

const HorizontalLayout: React.FC<ComponentsProps> = ({
  children,
  align = 'center',
  justify = 'flex-start',
  childGap = 0,
  inline = false,
  marginTop = 0,
  marginBottom = 0,
  marginLeft = 0,
  marginRight = 0,
  className,
  ...rest
}) => {
  return (
    <Root
      {...rest}
      className={classNames(`${childGap ? `hasChildren` : ''}`, className)}
      theme={{
        inline,
        align,
        justify,
        childGap,
        marginBottom,
        marginTop,
        marginLeft,
        marginRight,
      }}
    >
      {children}
    </Root>
  );
};

export default HorizontalLayout;
