/* eslint-disable no-console */

/**
 * Simple encapsulation for loggin
 */

// FIXME: Disable logs on production ?

const Logger = {
  info(...args: unknown[]) {
    console.log(...args);
  },
  warn(...args: unknown[]) {
    console.warn(...args);
  },
  error(...args: unknown[]) {
    console.error(...args);
  },
};

export default Logger;
