import { useContext, useEffect } from 'react';
import { useFormik } from 'formik';
import { Button, InputLabel } from '@material-ui/core';
import * as Yup from 'yup';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components/macro';
import { useTranslation } from 'react-i18next';
import { toast } from 'react-toastify';
import { useResponsiveContext } from '../contexts/ResponsiveContext';

import {
  ActivityIndicator,
  Typography,
  VerticalLayout,
  Icon,
} from '../components/ui';
import palette from '../components/ui/style/palette';
import font from '../components/ui/style/font';
import { AppContext } from '../contexts/AppContext';
import { AppActions } from '../contexts/app.reducer';
import spacing from '../components/ui/style/spacing';
import TextField from '../components/ui/TextField';
import { loadAppData } from '../util/data.helper';
import { login } from '../services/api.service';
import {
  LocalStorage,
  setLocalStorage,
  getLocalStorage,
  removeStorageItem,
} from '../services/storage.service';
import Layout from '../components/general/Layout';
import PivotIcon from '../assets/icons/pivot-text-color.svg';
import Logger from '../services/logger';
import { respondTo } from '../components/ui/style/dimensions';

const StyledButton = styled(Button)`
  width: 327px;
  border-radius: ${spacing.medium}px;
  font-size: ${font.size.mediumLg};
  text-transform: none;
  ${respondTo.tabletUp} {
    width: 496px;
  }
  ${respondTo.desktopUp} {
    width: 440px;
  }
`;

const BodyStyled = styled(VerticalLayout)`
  ${respondTo.onlyMobile} {
    padding-left: calc((100vw - 328px) / 2);
    padding-right: calc((100vw - 328px) / 2);
  }
  min-height: calc(100vh - 140px);
  .pivot-icon {
    img {
      width: 100px;
    }
  }
`;

const WelcomeStyled = styled(Typography)`
  font-size: ${font.size.medium};
  ${respondTo.desktopUp} {
    font-size: ${font.size.mediumLg};
  }
`;

const WelcomeTitleStyled = styled(Typography)`
  font-size: ${font.size.large};
  ${respondTo.desktopUp} {
    font-size: ${font.size.xxLarge};
  }
`;

const Login: React.FC = () => {
  const history = useHistory();
  const { t } = useTranslation();
  const params = useParams<{ cycleId: string }>();
  const responsiveContext = useResponsiveContext();

  const { dispatch } = useContext(AppContext);

  useEffect(() => {
    const token = getLocalStorage(LocalStorage.AUTH_TOKEN);
    const rememberMe = getLocalStorage(LocalStorage.REMEMBER_ME);
    if (token && rememberMe === 'true') {
      history.push('/survey');
    }
  }, [history]);

  const formik = useFormik({
    initialValues: { username: '', password: '', rememberMe: 'false' },
    validationSchema: Yup.object().shape({
      username: Yup.string().required(t('Student ID is a required field')),
    }),
    onSubmit: async values => {
      const { username, rememberMe } = values;
      try {
        const { cycleId = '' } = params;
        const { token, student } = await login(cycleId, username);
        setLocalStorage(LocalStorage.AUTH_TOKEN, token);
        const payload = await loadAppData(cycleId, token, student);
        setLocalStorage(LocalStorage.CYCLE, cycleId);
        setLocalStorage(LocalStorage.STUDENT_DATA, JSON.stringify(student));
        const currentBaseline = !!getLocalStorage(LocalStorage.BASE_LINE);
        if (payload.metaData.isBaseline !== currentBaseline) {
          removeStorageItem(LocalStorage.FORM_STATE);
          removeStorageItem(LocalStorage.CURRENT_STEP);
        }
        setLocalStorage(
          LocalStorage.BASE_LINE,
          JSON.stringify(payload.metaData.isBaseline)
        );
        dispatch({
          type: AppActions.logIn,
          payload,
        });
        setLocalStorage(LocalStorage.REMEMBER_ME, `${rememberMe}`);
        if (student.onboardingComplete) {
          history.push('/survey');
        } else {
          history.push('/');
        }
      } catch (e) {
        let message = 'Error, please try again';
        if (e.response) {
          const {
            response: { data },
          } = e;
          if (data.message) message = data.message;
        }
        toast.error(message);
      }
    },
  });

  return (
    <Layout
      hideHeader={responsiveContext.onlyMobile || responsiveContext.onlyTablet}
      responsive
      color="light"
      hideLogo
      showHeaderActions={false}
    >
      <BodyStyled align="center" justify="center" childGap={spacing.mediumPlus}>
        <Icon
          icon={PivotIcon}
          onClick={() => Logger.info('implement me')}
          alt="Pivot"
          className="pivot-icon"
        />

        <WelcomeTitleStyled
          color={palette.purple.p4}
          align="center"
          size="xxLarge"
          weight="bold"
        >
          {t('Wellbeing for Learning')}
        </WelcomeTitleStyled>
        <WelcomeStyled
          color={palette.purple.p4}
          align="center"
          size="mediumLg"
          weight="medium"
        >
          {t('Welcome to your weekly wellbeing check-in.')}
        </WelcomeStyled>
        <form onSubmit={formik.handleSubmit} noValidate>
          <VerticalLayout childGap={spacing.mediumPlus} align="center">
            <InputLabel htmlFor="username" />
            <TextField
              required
              fullWidth
              id="username"
              name="username"
              placeholder={t('Your Student ID')}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={!!(formik.touched.username && formik.errors.username)}
              helperText={
                formik.touched.username && formik.errors.username
                  ? formik.errors.username
                  : ''
              }
            />
            <Typography color={palette.purple.p4} size="small" weight="medium">
              {t(
                'Please talk to your teacher if you have forgotten your Student ID.'
              )}
            </Typography>
            <ActivityIndicator active={formik.isSubmitting} />
            <StyledButton
              disabled={formik.isSubmitting}
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
            >
              {t('Log in')}
            </StyledButton>
          </VerticalLayout>
        </form>
      </BodyStyled>
    </Layout>
  );
};

export default Login;
