import styled, { css } from 'styled-components/macro';
import { useTranslation } from 'react-i18next';
import { Link, useHistory } from 'react-router-dom';
import { useContext, useMemo, useCallback } from 'react';
import { VerticalLayout, Icon, HorizontalLayout, Typography } from '../ui';
import Logger from '../../services/logger';
import spacing from '../ui/style/spacing';
import PivotTextIcon from '../../assets/icons/pivot-text.svg';
import PivotTextIconColor from '../../assets/icons/pivot-text-color.svg';
import PivotIcon from '../../assets/icons/pivot.svg';
import PivotIconColor from '../../assets/icons/pivot-color.svg';
import PlayIcon from '../../assets/icons/play.svg';
import PlayIconColor from '../../assets/icons/play-color.svg';
import QuestionIcon from '../../assets/icons/white_hand.svg';
import QuestionIconColor from '../../assets/icons/blue_hand.svg';
import LogoutIcon from '../../assets/icons/logout.svg';
import LogoutIconColor from '../../assets/icons/logout-color.svg';
import Header from './Header';
import { useResponsiveContext } from '../../contexts/ResponsiveContext';
import { clearLocalStorage } from '../../services/storage.service';
import { AppContext } from '../../contexts/AppContext';
import { AppActions } from '../../contexts/app.reducer';
import font from '../ui/style/font';
import { respondTo } from '../ui/style/dimensions';
import palette from '../ui/style/palette';

type ColorType = 'dark' | 'light';

type LayoutProps = {
  color?: ColorType;
  bgHeader?: string;
  colorHeader?: string;
  hideFooter?: boolean;
  hideHeader?: boolean;
  showHeaderActions?: boolean;
  responsive?: boolean;
  full?: boolean;
  hideLogo?: boolean;
};

type ContainerProps = {
  responsive?: boolean;
  hideFooter?: boolean;
  full?: boolean;
};

const StyledContainer = styled.div<ContainerProps>`
  ${props =>
    props.responsive &&
    css`
      font-size: ${font.responsive.mobile};
      ${respondTo.tabletLSUp} {
        font-size: ${font.responsive.tablet};
      }
      ${respondTo.desktopUp} {
        font-size: ${font.responsive.desktop};
      }
    `}
`;

const Body = styled.div<ContainerProps>`
  min-height: 400px;
  ${props =>
    props.full &&
    css`
      width: 100%;
    `}
  ${props =>
    !props.hideFooter &&
    css`
      padding-bottom: ${spacing.xxxLarge}px;
    `}
`;

const Footer = styled.div`
  position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: 40px;
  width: 100vw;
  background-color: ${palette.grey.g1};
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: ${spacing.smallPlus}px;
  ${respondTo.tabletLSUp} {
    padding: 12px 120px;
  }
  ${respondTo.onlyMobile} {
    height: 72px;
    padding-top: 10px;
  }
`;

const StyledLink = styled(Link)`
  text-decoration: none;
  font-size: ${font.size.xSmall};
  font-weight: ${font.weight.medium};
  color: ${palette.purple.p4};
  ${respondTo.tabletUp} {
    font-size: ${font.size.small};
  }
`;

const date = new Date();
const fullYear = date.getFullYear();

type IconHeaderProps = {
  dir?: 'left' | 'right';
};
const IconHeaderStyle = styled.div<IconHeaderProps>`
  ${props =>
    props.dir === 'right'
      ? css`
          margin-left: 32px;
        `
      : css`
          margin-right: 32px;
        `}
`;

const IconTextStyle = styled.span`
  text-transform: uppercase;
  margin-left: 10px;
`;

const Layout: React.FC<LayoutProps> = ({
  children,
  hideHeader,
  showHeaderActions = true,
  color = 'dark',
  bgHeader,
  colorHeader,
  responsive,
  hideFooter = false,
  full = false,
  hideLogo = false,
}) => {
  const responsiveContext = useResponsiveContext();
  const { state, dispatch } = useContext(AppContext);
  const { t } = useTranslation();
  const history = useHistory();

  const layoutColors = useMemo(() => {
    if (color === 'dark') {
      return {
        PivotIcon,
        PivotTextIcon,
        QuestionIcon,
        PlayIcon,
        LogoutIcon,
        bgHeader: bgHeader || palette.purple.p4,
        colorHeader: colorHeader || palette.white,
      };
    }
    return {
      PivotIcon: PivotIconColor,
      PivotTextIcon: PivotTextIconColor,
      QuestionIcon: QuestionIconColor,
      PlayIcon: PlayIconColor,
      LogoutIcon: LogoutIconColor,
      bgHeader: bgHeader || palette.white,
      colorHeader: colorHeader || palette.purple.p4,
    };
  }, [bgHeader, color, colorHeader]);
  let showText = false;
  let pivotIcon = layoutColors.PivotIcon;
  if (responsiveContext.desktopUp) {
    showText = true;
    pivotIcon = layoutColors.PivotTextIcon;
  }
  const headerLogo = (
    <IconHeaderStyle>
      <Icon
        icon={pivotIcon}
        onClick={() => Logger.info('implement me')}
        alt="Pivot"
      />
    </IconHeaderStyle>
  );

  const routerState = useMemo(() => {
    const {
      location: { pathname },
    } = history;
    let stateHistory = { from: pathname };

    if (pathname === '/survey') {
      stateHistory = Object.assign(stateHistory, { backToSurvey: true });
    }

    return stateHistory;
  }, [history]);

  const onHelpClick = useCallback(() => {
    history.push('/help', routerState);
  }, [history, routerState]);

  const onWatchAnimationClick = useCallback(() => {
    history.push('/', routerState);
  }, [history, routerState]);

  const headerActions = [
    <IconHeaderStyle key="help-action" dir="right">
      <Icon
        onClick={onHelpClick}
        icon={layoutColors.QuestionIcon}
        alt={t('Ask for help')}
        childGap={spacing.smallPlus}
      >
        {showText && <IconTextStyle>{t('Ask for help')}</IconTextStyle>}
      </Icon>
    </IconHeaderStyle>,
    <IconHeaderStyle key="watch-action" dir="right">
      <Icon
        onClick={onWatchAnimationClick}
        icon={layoutColors.PlayIcon}
        alt={t('Why wellbeing matters')}
        childGap={spacing.smallPlus}
      >
        {showText && (
          <IconTextStyle>{t('Why wellbeing matters')}</IconTextStyle>
        )}
      </Icon>
    </IconHeaderStyle>,
  ];

  if (state.authToken) {
    headerActions.push(
      <IconHeaderStyle key="logout-action" dir="right">
        <Icon
          onClick={() => {
            clearLocalStorage();
            dispatch({ type: AppActions.logOut });
            history.replace(`/login/${state.cycle}`);
          }}
          icon={layoutColors.LogoutIcon}
          alt={t('Logout')}
          childGap={spacing.smallPlus}
        >
          {showText && <IconTextStyle>{t('Logout')}</IconTextStyle>}
        </Icon>
      </IconHeaderStyle>
    );
  }

  return (
    <StyledContainer responsive={responsive}>
      <VerticalLayout>
        {!hideHeader && (
          <Header
            bgColor={layoutColors.bgHeader}
            color={layoutColors.colorHeader}
            showHeaderActions={showHeaderActions}
            logo={headerLogo}
            hideLogo={hideLogo}
            actions={headerActions}
          />
        )}
        <VerticalLayout align="center">
          <Body full={full}>{children}</Body>
        </VerticalLayout>
      </VerticalLayout>
      {!hideFooter && (
        <Footer>
          <HorizontalLayout
            childGap={spacing.mediumPlus}
            align={responsiveContext.onlyMobile ? 'flex-start' : 'center'}
          >
            <Typography size="small">Copyright  2014 - {fullYear}</Typography>
          </HorizontalLayout>
          <HorizontalLayout
            childGap={spacing.mediumPlus}
            justify="flex-end"
            align={responsiveContext.onlyMobile ? 'flex-start' : 'center'}
          >
            <StyledLink to="/legal/terms-of-use">
              {t('Terms of Use')}
            </StyledLink>
            <StyledLink to="/legal/privacy-policy">
              {t('Privacy Policy')}
            </StyledLink>
          </HorizontalLayout>
        </Footer>
      )}
    </StyledContainer>
  );
};

export default Layout;
