import { createContext, useEffect, useReducer } from 'react';
import { ResponseMetaData, SchoolClass, Student } from '../services/api.types';
import { reducer, AppState, AppAction, AppActions } from './app.reducer';
import Logger from '../services/logger';
import { ActivityIndicator } from '../components/ui';
import { loadAppData } from '../util/data.helper';
import { WizardType } from '../components/SurveyWizard/wizard.types';
import { getLocalStorage, LocalStorage } from '../services/storage.service';

const getStudentProfileFromStorage = (): Student => {
  const value = getLocalStorage(LocalStorage.STUDENT_DATA);
  if (!value) return {} as Student;
  return JSON.parse(value) as Student;
};

const initialState: AppState = {
  isAuthenticating: true,
  studentProfile: getStudentProfileFromStorage(),
  classData: {} as SchoolClass,
  questions: {} as WizardType,
  metaData: {} as ResponseMetaData,
  isAnswered: false,
  authToken: getLocalStorage(LocalStorage.AUTH_TOKEN),
  cycle: getLocalStorage(LocalStorage.CYCLE),
};

export const AppContext = createContext<{
  state: AppState;
  dispatch: React.Dispatch<AppAction>;
}>({ state: initialState, dispatch: () => {} });

export const AppProvider: React.FC = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    const initAuthState = async () => {
      try {
        if (!state.authToken) return;
        const payload = await loadAppData(
          state.cycle,
          state.authToken,
          state.studentProfile
        );
        dispatch({
          type: AppActions.logIn,
          payload,
        });
      } catch (e) {
        Logger.error(e);
      } finally {
        dispatch({ type: AppActions.appReady });
      }
    };
    initAuthState();
  }, [state.authToken, state.studentProfile, state.cycle]);

  return (
    <AppContext.Provider
      value={{
        state,
        dispatch,
      }}
    >
      <ActivityIndicator active={state.isAuthenticating}>
        {children}
      </ActivityIndicator>
    </AppContext.Provider>
  );
};
