import styled from 'styled-components/macro';
import classNames from 'classnames';
import { useEffect, useState } from 'react';

import platypus_1 from '../../assets/icons/platypus-01.svg';
import platypus_2 from '../../assets/icons/platypus-02.svg';
import platypus_3 from '../../assets/icons/platypus-03.svg';
import platypus_4 from '../../assets/icons/platypus-04.svg';
import { breakpoints, respondTo } from '../ui/style/dimensions';
import palette from '../ui/style/palette';
import spacing from '../ui/style/spacing';
import { Clickable } from '../ui';

type WellbeingProgressBarProps = {
  currentStep: number;
  onChangeStep: (nextStep: number) => void;
};

const StyledProgressBar = styled.div`
  background-color: ${palette.purple.p4};
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    min-width: 375px;
    ${respondTo.tabletUp} {
      min-width: ${breakpoints.tabletWidth - 20}px;
    }
    ${respondTo.tabletLSUp} {
      min-width: ${breakpoints.tabletLanscapeWidth - 40}px;
    }
    color: ${palette.white};
    justify-content: center;
    padding: ${spacing.xxLarge}px;
    counter-reset: section;
    li {
      list-style-type: none;
      width: 40px;
      ${respondTo.tabletUp} {
        width: 80px;
      }
      ${respondTo.tabletLSUp} {
        width: 120px;
      }
      height: 4px;
      background-color: ${palette.purple.p10};
      margin: 0 ${spacing.mediumPlus}px;
      position: relative;
      counter-increment: section;
      img {
        position: absolute;
        top: -48px;
        right: -70px;
        z-index: 10;
        pointer-events: none;
        width: 95px;
        &.platypus_1 {
          width: 120px;
          top: -50px;
          right: -110px;
        }
      }
      &::before,
      &::after {
        width: 30px;
        height: 30px;
        display: block;
        border-radius: 50%;
        border: 4px solid ${palette.purple.p10};
        content: counter(section);
        text-indent: -9999px;
        font-size: 14px;
        ${respondTo.desktopUp} {
          font-size: 18px;
        }
        top: -13px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        left: -35px;
        position: absolute;
      }
      &.active {
        cursor: pointer;
        &::before,
        &::after {
          text-indent: 0;
          background-color: ${palette.purple.p10};
        }
      }
      &.hide-circle {
        &::after,
        &::before {
          display: none;
        }
      }
      &.hide-line {
        width: 0px;
      }
    }
  }
  .last-step {
    width: 0px;
  }
`;

const WellbeingProgressBar: React.FC<WellbeingProgressBarProps> = ({
  currentStep = 1,
  onChangeStep,
}) => {
  const [maxStep, setMaxStep] = useState<number>(currentStep);

  useEffect(() => {
    if (maxStep < currentStep) {
      setMaxStep(currentStep);
    }
  }, [currentStep, maxStep]);

  const onClickStep = (step: number) => {
    if (step <= maxStep) onChangeStep(step - 1);
  };

  return (
    <StyledProgressBar data-testid="wb_progressbar">
      <ul>
        <Clickable
          onClick={() => {
            onClickStep(1);
          }}
        >
          <li className={classNames([{ active: maxStep >= 1 }])}>
            {currentStep === 2 && (
              <img
                className="platypus_1"
                alt="platypus_1"
                src={platypus_1}
                data-testid="step_2_img"
              />
            )}
          </li>
        </Clickable>
        <Clickable
          onClick={() => {
            onClickStep(2);
          }}
        >
          <li
            className={classNames([
              { active: maxStep >= 2, 'hide-circle': currentStep === 2 },
            ])}
          >
            {currentStep === 3 && (
              <img alt="platypus_2" src={platypus_2} data-testid="step_3_img" />
            )}
          </li>
        </Clickable>
        <Clickable
          onClick={() => {
            onClickStep(3);
          }}
        >
          <li
            className={classNames([
              { active: maxStep >= 3, 'hide-circle': currentStep === 3 },
            ])}
          >
            {currentStep === 4 && (
              <img alt="platypus_3" src={platypus_3} data-testid="step_4_img" />
            )}
          </li>
        </Clickable>
        <Clickable
          onClick={() => {
            onClickStep(4);
          }}
        >
          <li
            className={classNames([
              {
                active: maxStep >= 4,
                'hide-circle': currentStep === 4,
              },
            ])}
          >
            {currentStep === 5 && (
              <img alt="platypus_4" src={platypus_4} data-testid="step_5_img" />
            )}
          </li>
        </Clickable>
        <Clickable
          onClick={() => {
            onClickStep(5);
          }}
          className="last-step"
        >
          <li
            className={classNames([
              {
                active: maxStep >= 5,
                'hide-circle': currentStep === 5,
                'hide-line': true,
              },
            ])}
          />
        </Clickable>
      </ul>
    </StyledProgressBar>
  );
};

export default WellbeingProgressBar;
