import { useRef, useState } from 'react';
import styled from 'styled-components/macro';
import Slider from 'react-slick';
import { Icon, HorizontalLayout } from '../ui';
import spacing from '../ui/style/spacing';
import wb_onboarding_01 from '../../assets/img/onboarding/01.svg';
import wb_onboarding_02 from '../../assets/img/onboarding/02.svg';
import wb_onboarding_03 from '../../assets/img/onboarding/03.svg';
import wb_onboarding_04 from '../../assets/img/onboarding/04.svg';
import wb_onboarding_05 from '../../assets/img/onboarding/05.svg';
import wb_onboarding_06 from '../../assets/img/onboarding/06.svg';
import left_arrow from '../../assets/icons/left-arrow.svg';
import right_arrow from '../../assets/icons/right-arrow.svg';

const data = [
  {
    id: 1,
    alt: 'wb_onboarding_01',
    src: wb_onboarding_01,
  },
  {
    id: 2,
    alt: 'wb_onboarding_02',
    src: wb_onboarding_02,
  },
  {
    id: 3,
    alt: 'wb_onboarding_03',
    src: wb_onboarding_03,
  },
  {
    id: 4,
    alt: 'wb_onboarding_04',
    src: wb_onboarding_04,
  },
  {
    id: 5,
    alt: 'wb_onboarding_05',
    src: wb_onboarding_05,
  },
  {
    id: 6,
    alt: 'wb_onboarding_06',
    src: wb_onboarding_06,
  },
];

export const LENGTH_SLIDER = data.length;

type WellbeingSliderProps = {
  afterChange?: (index: number) => void;
};

const WellbeingSliderStyled = styled.div`
  width: calc(100vw / 3 * 2);
  margin: 0 8px;
  .slick-dots li button {
    pointer-events: none;
  }

  .slick-dots {
    white-space: nowrap;
  }
`;

type IconProps = {
  hide?: boolean;
};

const IconStyled = styled(Icon)<IconProps>`
  width: ${spacing.xLarge}px;
  height: ${spacing.xLarge}px;
  visibility: ${props => (props.hide ? 'hidden' : 'visible')};
  img {
    width: 100%;
  }
}
`;

const WellbeingSlider: React.FC<WellbeingSliderProps> = ({ afterChange }) => {
  const sliderRef = useRef<Slider>(null);
  const [currentSlide, setCurrentSlide] = useState(0);

  const beforeChange = (_current: number, next: number) => {
    setCurrentSlide(next);
  };

  return (
    <HorizontalLayout
      justify="space-around"
      marginTop={spacing.xxLarge}
      marginBottom={spacing.large}
    >
      <IconStyled
        icon={left_arrow}
        onClick={() => {
          if (sliderRef && sliderRef.current) {
            sliderRef.current?.slickPrev();
          }
        }}
        alt="left_arrow"
        hide={currentSlide === 0}
      />
      <WellbeingSliderStyled>
        <Slider
          afterChange={afterChange}
          beforeChange={beforeChange}
          infinite={false}
          ref={sliderRef}
          arrows={false}
          dots
        >
          {data.map(item => (
            <div key={item.id}>
              <img alt={item.alt} src={item.src} />
            </div>
          ))}
        </Slider>
      </WellbeingSliderStyled>
      <IconStyled
        icon={right_arrow}
        onClick={() => {
          if (sliderRef && sliderRef.current) {
            sliderRef.current?.slickNext();
          }
        }}
        alt="right_arrow"
        hide={currentSlide === data.length - 1}
      />
    </HorizontalLayout>
  );
};

export default WellbeingSlider;
