export const LocalStorage = {
  STUDENT_DATA: 'STUDENT_DATA',
  AUTH_TOKEN: 'AUTH_TOKEN',
  CYCLE: 'CYCLE',
  REMEMBER_ME: 'REMEMBER_ME',
  CURRENT_STEP: 'CURRENT_STEP',
  FORM_STATE: 'FORM_STATE',
  BASE_LINE: 'BASE_LINE',
};

export const getLocalStorage = (key: string, defaultValue = ''): string => {
  return localStorage.getItem(key) || defaultValue;
};

export const setLocalStorage = (key: string, value: string): void => {
  localStorage.setItem(key, value);
};

export const removeStorageItem = (key: string): void => {
  localStorage.removeItem(key);
};

export const clearLocalStorage = (): void => {
  localStorage.removeItem(LocalStorage.STUDENT_DATA);
  localStorage.removeItem(LocalStorage.AUTH_TOKEN);
  localStorage.removeItem(LocalStorage.CYCLE);
  localStorage.removeItem(LocalStorage.REMEMBER_ME);
  localStorage.removeItem(LocalStorage.CURRENT_STEP);
  localStorage.removeItem(LocalStorage.FORM_STATE);
  localStorage.removeItem(LocalStorage.BASE_LINE);
};
