import styled from 'styled-components/macro';
import { respondTo } from '../ui/style/dimensions';

type ComponentsProps = {
  marginTop?: number;
  marginBottom?: number;
};

type StyledProps = {
  theme: ComponentsProps;
};

const Root = styled.div<StyledProps>`
  display: flex;
  flex-direction: column;
  width: calc(100% - 16px);
  ${respondTo.desktopUp} {
    flex-direction: row;
    justify-content: space-between;
  }
`;

const OptionContainer: React.FC<ComponentsProps> = ({
  children,
  marginTop = 0,
  marginBottom = 0,
  ...rest
}) => {
  return (
    <Root {...rest} theme={{ marginBottom, marginTop }}>
      {children}
    </Root>
  );
};

export default OptionContainer;
