import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import styled, { css } from 'styled-components/macro';
import classNames from 'classnames';
import GreenHand from '../../assets/icons/green-hand.svg';
import RedHand from '../../assets/icons/red-hand.svg';

import { px2em } from '../../util/helpers';
import { Clickable, HorizontalLayout, Typography, VerticalLayout } from '../ui';
import spacing from '../ui/style/spacing';
import { Categories } from './wizard.consts';
import { FormFieldType } from './wizard.types';
import { AppContext } from '../../contexts/AppContext';
import { respondTo } from '../ui/style/dimensions';
import palette from '../ui/style/palette';

type BehaviourStepProps = {
  setFieldValue: (field: string, value: FormFieldType) => void;
  value: FormFieldType;
};

type MarginProps = {
  marginBottom?: number;
  marginTop?: number;
};

const StyledGrid = styled.div`
  display: grid;
  grid-template-columns: 100%;
  grid-gap: 30px;
  ${respondTo.tabletUp} {
    grid-template-columns: 50% 50%;
    grid-gap: 50px;
  }
  ${respondTo.desktopUp} {
    grid-template-columns: auto auto auto;
    grid-gap: 80px;
  }
`;

const StyledBehaviourItem = styled(VerticalLayout)`
  padding: ${spacing.large}px;
  box-shadow: 0px 4px 10px rgba(153, 159, 173, 0.2);
  border-radius: ${spacing.mediumPlus}px;
  width: 232px;
  height: 232px;
`;
const StyledIcon = styled.img`
  margin-bottom: ${spacing.medium}px;
`;

const StyledThumb = styled(Clickable)`
  background: ${palette.grey.g3};
  box-shadow: 0px 4px 4px rgba(153, 159, 173, 0.4);
  border-radius: 10px;
  height: 64px;
  width: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.35s;
  img {
    transition: all 0.45s;
  }
  &:hover,
  &.active {
    background: #e9e5f7;
  }
  &:hover {
    img {
      transform: scale(1.1) rotate(15deg);
    }
  }
`;
export const QuestionStyle = styled.div<MarginProps>`
  padding: 0 ${spacing.large}px;
  ${respondTo.tabletLSUp} {
    padding: 0 75px;
  }
  ${props =>
    props.marginTop &&
    css`
      margin-top: ${px2em(props.marginTop)};
    `}
  ${props =>
    props.marginBottom &&
    css`
      margin-bottom: ${px2em(props.marginBottom)};
    `}
`;

const BehaviourQuestionStep: React.FC<BehaviourStepProps> = ({
  value,
  setFieldValue,
}) => {
  const { t } = useTranslation();
  const { state } = useContext(AppContext);

  const handleClick = (category: string, categoryValue: number) => {
    const updatedValue = Object.assign(value, { [category]: categoryValue });
    const shouldAnwerMore = Object.values(updatedValue).some(v => v === 0);
    setFieldValue('behaviour', updatedValue);
    if (!shouldAnwerMore) {
      setFieldValue('isBehaviourCompleted', 1);
    }
  };
  const getActive = (category: string, categoryValue: number) => {
    // eslint-disable-next-line prefer-object-spread
    const fieldValues = Object.assign({}, value) as { [key: string]: number };
    return fieldValues[category] === categoryValue;
  };

  return (
    <VerticalLayout align="center" responsive>
      <QuestionStyle
        marginBottom={spacing.xxLarge}
        marginTop={spacing.mediumPlus}
      >
        <Typography
          weight="normal"
          size="largePlus"
          color={palette.blue.b2}
          align="center"
          responsive
        >
          Finally, {state.studentProfile.firstName}
        </Typography>
        <Typography
          weight="bold"
          size="xxLarge"
          color={palette.purple.p4}
          align="center"
          responsive
        >
          {t('How have you been going this week with')}
        </Typography>
      </QuestionStyle>
      <HorizontalLayout justify="center" marginTop={30}>
        <StyledGrid>
          {Categories.map(category => (
            <StyledBehaviourItem key={category.label} align="center">
              <Typography color={palette.purple.p4} weight="bold" size="large">
                {category.label}
              </Typography>
              <StyledIcon src={category.icon} />
              <HorizontalLayout childGap={spacing.medium} justify="center">
                <StyledThumb
                  className={classNames({
                    active: getActive(category.value, 1),
                  })}
                  onClick={() => handleClick(category.value, 1)}
                >
                  <img alt="red hand" src={RedHand} />
                </StyledThumb>
                <StyledThumb
                  className={classNames({
                    active: getActive(category.value, -1),
                  })}
                  onClick={() => handleClick(category.value, -1)}
                >
                  <img alt="green hand" src={GreenHand} />
                </StyledThumb>
              </HorizontalLayout>
            </StyledBehaviourItem>
          ))}
        </StyledGrid>
      </HorizontalLayout>
    </VerticalLayout>
  );
};

export default BehaviourQuestionStep;
