import { useFormik } from 'formik';
import { TextField, Button, Container } from '@material-ui/core';
import * as Yup from 'yup';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import Logger from '../services/logger';
import {
  ActivityIndicator,
  Typography,
  VerticalLayout,
} from '../components/ui';

import spacing from '../components/ui/style/spacing';

const Login: React.FC = () => {
  const history = useHistory();
  const { t } = useTranslation();

  const formik = useFormik({
    initialValues: { username: '' },
    validationSchema: Yup.object().shape({
      username: Yup.string().required(),
    }),
    onSubmit: async values => {
      const { username } = values;

      try {
        // TODO: Implement me
        Logger.info(username);
        history.push('/reset-password');
      } catch (e) {
        const { message = '' } = e;
        if (message) formik.setFieldError('username', message);
        Logger.error(e);
      }
    },
  });

  return (
    <Container component="main" maxWidth="xs">
      <div>
        <Typography size="xLarge" weight="medium">
          Forgot Password
        </Typography>
        <Typography size="small">{t('Forgot password description')}</Typography>
        <form onSubmit={formik.handleSubmit} noValidate>
          <TextField
            required
            variant="outlined"
            margin="normal"
            fullWidth
            id="username"
            label="username"
            name="username"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={!!(formik.touched.username && formik.errors.username)}
            helperText={
              formik.touched.username && formik.errors.username
                ? formik.errors.username
                : ''
            }
          />

          <VerticalLayout align="center" childGap={spacing.mediumPlus}>
            <ActivityIndicator active={formik.isSubmitting} />
            <Button
              disabled={formik.isSubmitting}
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
            >
              Submit
            </Button>
          </VerticalLayout>
        </form>
      </div>
    </Container>
  );
};

export default Login;
