import styled from 'styled-components/macro';
import { useCallback, useContext, useMemo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { toast } from 'react-toastify';
import { useHistory } from 'react-router-dom';
import { Typography, Button, Modal } from '../components/ui';
import palette from '../components/ui/style/palette';
import Layout from '../components/general/Layout';
import { respondTo } from '../components/ui/style/dimensions';
import { AppContext } from '../contexts/AppContext';
import {
  saveResponse,
  sendDistressNotification,
} from '../services/api.service';
import { useQuery } from '../hooks/useQuery';

import { DistressType } from '../consts';

import {
  CreateSurveyResponse,
  DistressLogPayload,
} from '../services/api.types';
import spacing from '../components/ui/style/spacing';
import font from '../components/ui/style/font';

const SupportContainer = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 388px;
  margin: 0 auto;
`;
const SupportBlock = styled.div`
  box-shadow: 0px 4px 10px rgba(153, 159, 173, 0.2);
  border-radius: 16px;
  padding: 16px;
  margin-bottom: 32px;
`;
const SupportButton = styled(Button)`
  && {
    font-size: 18px;
    color: ${palette.green.g4};
    padding: 20px 26px;
    width: 100%;
    background-color: rgba(120, 201, 156, 0.15);
    border-radius: 16px;
    text-transform: none;
    &:hover {
      background-color: rgba(120, 201, 156, 0.25);
    }
  }
`;
const SupportTitle = styled.h3`
  font-size: 16px;
  line-height: 24px;
  color: ${palette.purple.p4};
  margin-bottom: 32px;
`;

const SupportHeading = styled(Typography)`
  margin-bottom: 32px;
  font-weight: 700;
  color: ${palette.purple.p4};
  ${respondTo.desktopUp} {
    margin - bottom: 44px;
  }
`;

const CloseButton = styled(Button)`
  width: 100%;
`;

const ThankYouButton = styled(Button)`
  min-width: 388px;
  color: ${palette.purple.p4};
  font-size: ${font.size.mediumLg};
`;

const ContentModal = styled(Typography)`
  color: ${palette.grey.g2};
  padding: ${spacing.mediumPlus}px;
  ${respondTo.tabletLSUp} {
    padding: ${spacing.xLarge}px ${spacing.medium}px;
  }
`;

const FooterContainer = styled.div`
  width: 100%;
  display: flex;
  justify-contents: space-between;
  > button {
    margin: 0 5px;
    flex-grow: 1;
  }
`;
type SupportProps = {
  supportType: 'struggling' | 'dont-want-answer';
};

const Support: React.FC<SupportProps> = ({ supportType }) => {
  const { t } = useTranslation();
  const history = useHistory();
  const { state } = useContext(AppContext);
  const [emailSentTo, setEmailSendTo] = useState('');
  const [isOpen, setIsOpen] = useState(false);
  const [isOpenConfirm, setIsOpenConfirm] = useState(false);
  const [submitting, setSubmitting] = useState(false);
  const goBack = useCallback(async () => {
    if (supportType === 'dont-want-answer') {
      setIsOpenConfirm(true);
    } else {
      history.goBack();
    }
  }, [history, supportType]);

  const submitSurvey = useCallback(
    async confirm => {
      if (confirm) {
        try {
          setIsOpenConfirm(false);
          setSubmitting(true);
          const student = state.studentProfile;
          delete student.createdAt;
          delete student.onboardingComplete;
          const apiPayload = {
            student,
            metadata: {
              ...state.metaData,
            },
            skip: true,
            type: 'dont_want_answer',
          } as CreateSurveyResponse;
          await saveResponse(apiPayload);
          history.push('/thank-you');
        } catch (error) {
          toast.error(t('An error occurred when save survey'));
          setSubmitting(false);
        }
      } else {
        history.push('/survey');
      }
    },
    [history, state.metaData, state.studentProfile, t]
  );
  const distressLogPayload = useMemo(() => {
    if (state.authToken) {
      return {
        studentId: state.studentProfile.ID,
        studentName: state.studentProfile.firstName,
        classId: state.studentProfile.classId,
        providerId: state.studentProfile.providerId,
        schoolId: state.studentProfile.schoolId,
      } as DistressLogPayload;
    }
    return {} as DistressLogPayload;
  }, [
    state.authToken,
    state.studentProfile.ID,
    state.studentProfile.classId,
    state.studentProfile.firstName,
    state.studentProfile.providerId,
    state.studentProfile.schoolId,
  ]);
  const {
    isLoading: sendingDistressToTeacher,
    data: responseFromTeacher,
    isError: isErrorFromTeacher,
    mutate: sendToTeacher,
  } = useQuery((payload: DistressLogPayload) =>
    sendDistressNotification(payload)
  );

  const {
    isLoading: sendingDistressToLeader,
    data: responseFromLeader,
    isError: isErrorFromLeader,
    mutate: sendToLeader,
  } = useQuery((payload: DistressLogPayload) =>
    sendDistressNotification(payload)
  );
  const sendEmailToLeader = useCallback(() => {
    sendToLeader({
      ...distressLogPayload,
      type: DistressType.WELLBEING_LEADER,
    });
  }, [distressLogPayload, sendToLeader]);
  const sendEmailToTeacher = useCallback(() => {
    sendToTeacher({
      ...distressLogPayload,
      type: DistressType.TEACHER,
    });
  }, [distressLogPayload, sendToTeacher]);

  const goHelpPage = () => {
    history.push('/help', { backToOnboardingHelp: true });
  };

  useEffect(() => {
    if (isErrorFromTeacher || isErrorFromLeader) {
      toast.error(t('An error occured when sending distress email.'));
    }
  }, [isErrorFromTeacher, isErrorFromLeader, t]);

  useEffect(() => {
    if (responseFromTeacher) {
      setIsOpen(true);
      setEmailSendTo(
        `${t('We have sent an email to')} ${state.classData?.teacher?.name}`
      );
    }
  }, [responseFromTeacher, state, t]);

  useEffect(() => {
    if (responseFromLeader) {
      setIsOpen(true);
      setEmailSendTo(t('We have sent an email to your school wellbeing team'));
    }
  }, [responseFromLeader, t]);

  return (
    <Layout
      color="light"
      responsive
      bgHeader={palette.white}
      colorHeader={palette.purple.p4}
    >
      <SupportContainer>
        <SupportHeading responsive size="xLarge" align="center" weight="bold">
          {supportType === 'struggling'
            ? t("I'm struggling")
            : t("I don't want to do the check-in this week")}
        </SupportHeading>
        <SupportBlock>
          <SupportTitle>
            {t(
              'Would you like to reach out for help from your schools wellbeing team?'
            )}
          </SupportTitle>
          <SupportButton
            loading={sendingDistressToLeader}
            onClick={sendEmailToLeader}
            disabled={!!responseFromLeader}
          >
            {responseFromLeader
              ? t('We have sent an email')
              : t('Yes send them an email')}
          </SupportButton>
        </SupportBlock>
        <SupportBlock>
          <SupportTitle>
            {t('Would you like to reach out for help from your teacher ')}{' '}
            {state.classData?.teacher?.name}
          </SupportTitle>
          <SupportButton
            loading={sendingDistressToTeacher}
            onClick={sendEmailToTeacher}
            disabled={!!responseFromTeacher}
          >
            {responseFromTeacher
              ? t('We have sent an email')
              : t('Yes send them an email')}
          </SupportButton>
        </SupportBlock>
        <SupportBlock>
          <SupportTitle>
            {t('Would you like some help from outside your school?')}
          </SupportTitle>
          <SupportButton onClick={goHelpPage}>
            {t('Yes, show me')}
          </SupportButton>
        </SupportBlock>
        <CloseButton loading={submitting} variant="secondary" onClick={goBack}>
          {t('Close')}
        </CloseButton>
      </SupportContainer>
      <Modal
        isOpen={isOpen}
        setOpen={setIsOpen}
        title={t('The email was sent')}
        width={450}
        renderFooter={() => {
          return (
            <ThankYouButton
              responsive
              variant="secondary"
              onClick={() => setIsOpen(false)}
            >
              {t('Thank you!')}
            </ThankYouButton>
          );
        }}
      >
        <ContentModal size="medium" align="center">
          {emailSentTo}
        </ContentModal>
      </Modal>
      <Modal
        isOpen={isOpenConfirm}
        setOpen={setIsOpen}
        width={650}
        renderFooter={() => {
          return (
            <FooterContainer>
              <Button variant="primary" onClick={() => submitSurvey(true)}>
                {t('Yes')}
              </Button>
              <Button variant="secondary" onClick={() => submitSurvey(false)}>
                {t('No')}
              </Button>
            </FooterContainer>
          );
        }}
      >
        <ContentModal size="medium" align="center">
          {t('Are you sure you dont want to answer this week?')}
        </ContentModal>
      </Modal>
    </Layout>
  );
};

export default Support;
