import { WizardType } from '../components/SurveyWizard/wizard.types';
import { Student, SchoolClass, ResponseMetaData } from '../services/api.types';

export type AppAction = {
  type: string;
  payload?: unknown;
};

export type AppState = LoginPayload & {
  isAuthenticating: boolean;
};

export type LoginPayload = {
  studentProfile: Student;
  classData: SchoolClass;
  questions: WizardType;
  metaData: ResponseMetaData;
  isAnswered: boolean;
  isBaseline?: boolean;
  authToken: string;
  cycle: string;
};

export const AppActions = {
  setSudent: 'setSudent',
  logIn: 'logIn',
  appReady: 'appReady',
  logOut: 'logOut',
  updateOnboardingComplete: 'updateOnboardingComplete',
};

const INITIAL_STATE: AppState = {
  studentProfile: {} as Student,
  classData: {} as SchoolClass,
  questions: {} as WizardType,
  metaData: {} as ResponseMetaData,
  isAnswered: false,
  authToken: '',
  cycle: '',
  isAuthenticating: false,
};

export const reducer = (
  state: AppState = INITIAL_STATE,
  action: AppAction
): AppState => {
  switch (action.type) {
    case AppActions.logIn:
      const payload = action.payload as LoginPayload;
      return {
        ...state,
        ...payload,
      };
    case AppActions.appReady:
      return { ...state, isAuthenticating: false };
    case AppActions.logOut:
      return INITIAL_STATE;
    case AppActions.updateOnboardingComplete: {
      const stateCopied = { ...state };
      stateCopied.studentProfile.onboardingComplete = action.payload as boolean;
      return stateCopied;
    }
    default:
      return state;
  }
};
