import styled from 'styled-components/macro';
import font from './style/font';
import { px2em } from '../../util/helpers';

type Size =
  | 'xxSmall'
  | 'xSmall'
  | 'small'
  | 'base'
  | 'medium'
  | 'mediumPlus'
  | 'mediumLg'
  | 'large'
  | 'largePlus'
  | 'xLarge'
  | 'xxLarge';

type Weight = 'light' | 'normal' | 'medium' | 'bold' | 'black';

type TypographyProps = {
  size?: Size;
  uppercase?: boolean;
  color?: string;
  weight?: Weight;
  align?: string;
  responsive?: boolean;
  fontFamily?: string;
  textDecoration?: string;
  className?: string;
};

type StyledProps = {
  theme: {
    fontFamily?: string;
    fontSize: Size;
    fontWeight: Weight;
    uppercase: boolean;
    color: string;
    align: string;
    responsive: boolean;
    textDecoration?: string;
  };
};

const Root = styled.div<StyledProps>`
  color: ${props => props.theme.color || 'inherit'};
  font-size: ${props =>
    props.theme.responsive
      ? px2em(props.theme.fontSize)
      : props.theme.fontSize};
  line-height: ${props => props.theme.size};
  font-weight: ${props => props.theme.fontWeight};
  text-transform: ${props => (props.theme.uppercase ? 'uppercase' : 'unset')};
  text-align: ${props => props.theme.align};
  font-family: ${props => props.theme.fontFamily || 'CircularStd-Book'};
  text-decoration: ${props => props.theme.textDecoration};
`;

const Typography: React.FC<TypographyProps> = ({
  children,
  size = 'base',
  uppercase = false,
  color,
  weight = 'normal',
  align = 'left',
  responsive,
  fontFamily,
  textDecoration = 'none',
  className,
}) => {
  const fontSize = font.size[size];
  const fontWeight = font.weight[weight];
  return (
    <Root
      theme={{
        fontSize,
        fontWeight,
        uppercase,
        color,
        align,
        fontFamily,
        responsive,
        textDecoration,
      }}
      className={className}
    >
      {children}
    </Root>
  );
};

export default Typography;
