import { useTranslation } from 'react-i18next';
import { useHistory, useLocation } from 'react-router-dom';
import styled from 'styled-components';

import { Layout } from '../components/general';
import { Button, VerticalLayout } from '../components/ui';
import WellbeingTips from '../components/Onboarding/WellbeingTips';
import WhyWellbeing from '../components/Onboarding/WhyWellbeing';
import WellbeingHelps from '../components/Onboarding/WellbeingHelps';
import spacing from '../components/ui/style/spacing';

type LocationState = {
  backToSurvey?: boolean;
  backToOnboardingHelp?: boolean;
  from?: string;
};

type OnboardingHelpProps = {
  type: 'tips' | 'why' | 'helps';
};

const StyledContainer = styled.div`
  padding: 0px ${spacing.small}px ${spacing.xLarge}px ${spacing.small}px;
`;

const OnboardingHelp: React.FC<OnboardingHelpProps> = ({ type = 'why' }) => {
  const { t } = useTranslation();
  const { state } = useLocation<LocationState>();
  const isBackToSurveyMode = state && state.backToSurvey;
  const isOnboardingHelp = state && state.backToOnboardingHelp;
  const isBackToSupportPage =
    state && state.from && state.from.includes('support');

  const history = useHistory();

  const handleCloseButton = () => {
    if (isOnboardingHelp) {
      history.goBack();
      return;
    }

    const url = isBackToSurveyMode ? '/survey' : '/';

    history.push(url);
  };
  return (
    <Layout color="light" responsive>
      <StyledContainer>
        <VerticalLayout align="center" childGap={spacing.xxxLarge}>
          {type === 'tips' ? <WellbeingTips /> : null}

          {type === 'why' ? <WhyWellbeing /> : null}
          {type === 'helps' ? <WellbeingHelps /> : null}
          <Button responsive variant="secondary" onClick={handleCloseButton}>
            {t(isBackToSupportPage || isBackToSurveyMode ? 'Back' : 'Close')}
          </Button>
        </VerticalLayout>
      </StyledContainer>
    </Layout>
  );
};

export default OnboardingHelp;
