import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import { IoIosCloseCircleOutline } from 'react-icons/io';
import spacing from '../style/spacing';
import { breakpoints, respondTo } from '../style/dimensions';
import font from '../style/font';

const useStyles = makeStyles(theme => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  title: {
    textAlign: 'center',
    fontSize: `${font.size.mediumLg}`,
    [respondTo.desktopUp]: {
      fontSize: `${font.size.xLarge}`,
    },
  },
  paper: (props: { width?: number }) => ({
    position: 'relative',
    boxShadow: '0px 0px 30px rgba(162, 173, 199, 0.2)',
    borderRadius: `${spacing.mediumPlus}px`,
    border: 'none',
    backgroundColor: theme.palette.background.paper,
    maxWidth: `${breakpoints.mobileLanscapeWidth - spacing.large}px`,
    textAlign: 'center',
    padding: `${spacing.medium}px`,
    [respondTo.desktopUp]: {
      width: `${props?.width || breakpoints.tabletLanscapeWidth - 150}px`,
      maxWidth: 'max-content',
      padding: `${spacing.xxxLarge - 4}px`,
    },
  }),
  closeButton: {
    position: 'absolute',
    top: `${spacing.mediumPlus}px`,
    right: `${spacing.mediumPlus}px`,
    cursor: 'pointer',
    [respondTo.desktopUp]: {
      top: `${spacing.large}px`,
      right: `${spacing.large}px`,
    },
  },
  footer: {
    display: 'flex',
    justifyContent: 'center',
    padding: `${spacing.mediumPlus}px`,
  },
}));

type ModalProps = {
  isOpen: boolean;
  setOpen: (open: boolean) => void;
  title?: React.ReactNode | undefined;
  children: React.ReactNode;
  renderFooter?: () => React.ReactNode;
  width?: number;
  showClose?: boolean;
};

function TransitionsModal({
  isOpen = false,
  setOpen,
  title,
  children,
  renderFooter,
  showClose,
  width,
}: ModalProps) {
  const classes = useStyles({ width });

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Modal
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      className={classes.modal}
      open={isOpen}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Fade in={isOpen}>
        <div className={classes.paper}>
          {title && (
            <h2 className={classes.title} id="transition-modal-title">
              {title}
            </h2>
          )}
          {showClose && (
            <IoIosCloseCircleOutline
              className={classes.closeButton}
              size={32}
              onClick={handleClose}
            />
          )}
          <div id="transition-modal-description">{children}</div>
          {renderFooter && (
            <div className={classes.footer}>{renderFooter()}</div>
          )}
        </div>
      </Fade>
    </Modal>
  );
}

TransitionsModal.defaultProps = {
  renderFooter: undefined,
  title: undefined,
  showClose: false,
  width: undefined,
};

export default TransitionsModal;
