/**
 * A simple adatper to parse the api questions, add the 2 custom questions and return data ready for the wizard
 * @param questions
 */
const apiQuestion2Wizard = (studentQuestion: StudentQuestion): WizardType => {
  const { questions } = studentQuestion;

  const wizardQuestion = questions.map(apiQuestion => ({
    id: apiQuestion.ID,
    type: apiQuestion.type,
    question: apiQuestion.question,
    explanation: apiQuestion.explanation,
    fieldsType: FieldTypesConst.RADIO_BUTTON,
    fields: questionScale.fields,
  }));

  const steps = studentQuestion.isBaseline
    ? [...wizardQuestion]
    : [customQuestions[0], ...wizardQuestion, customQuestions[1]];
  return { steps } as WizardType;
};

/**
 * Core Helper function to load and format the common data needed when the student login or the app is reloaded
 * Load profile, class data, questions for current iteration and metadata
 * This helper alos utilise the apiQuestion2Wizard to map and format the questions common from the API to a format valid for the survey wizard
 */
export const loadAppData = async (
  cycle: string,
  authToken: string,
  studentProfile: Student
): Promise<LoginPayload> => {
  try {
    const classData = await getClassDetails(studentProfile.classId);
    const studentQuestion = await getIterationQuestions(cycle);
    const checkAnswered = await getCheckAnswer(
      studentProfile.ID,
      studentQuestion.iteration.ID
    );
    return {
      studentProfile,
      classData,
      questions: !studentQuestion
        ? ({} as WizardType)
        : apiQuestion2Wizard(studentQuestion),
      metaData: {
        teacherId: classData.teacher.ID,
        cycleId: studentQuestion?.cycleId,
        iterationId: studentQuestion?.iteration.ID,
        isBaseline: studentQuestion.isBaseline,
      },
      isAnswered: checkAnswered.result,
      authToken,
      cycle,
    };
  } catch (e) {
    throw new Error(`Error loading student data: ${e} `);
  }
};
