/* eslint-disable @typescript-eslint/no-explicit-any */
import { useCallback, useContext, useEffect } from 'react';
import { FormikValues, useFormik } from 'formik';
import * as Yup from 'yup';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import {
  Button,
  HorizontalLayout,
  Typography,
  VerticalLayout,
  ActivityIndicator,
} from '../ui';

import WizardStep from './WizardStep';
import { Step, WizardType } from './wizard.types';
import spacing from '../ui/style/spacing';
import { AppContext } from '../../contexts/AppContext';
import { Categories, FieldTypesConst } from './wizard.consts';
import { Survey, CreateSurveyResponse } from '../../services/api.types';
import { saveResponse } from '../../services/api.service';

import { StyledRoot, FormBody, ButtonsContainer } from './styles';
import {
  LocalStorage,
  getLocalStorage,
  setLocalStorage,
} from '../../services/storage.service';
import { customQuestions } from '../../util/data.helper';

type SurveyWizardProps = {
  wizard: WizardType;
  onChange: (cur: number) => void;
  currentStep: number;
};

const SurveyWizard: React.FC<SurveyWizardProps> = ({
  wizard,
  onChange,
  currentStep,
}) => {
  const { state } = useContext(AppContext);
  const { t } = useTranslation();
  const history = useHistory();

  const isLastStep = currentStep === wizard.steps.length - 1;
  const { steps } = wizard;

  /**
   * Get wizard initial values
   * @returns form initial values
   */
  const getInitialValues = () => {
    // Check persisted state
    const persistedState = getLocalStorage(LocalStorage.FORM_STATE);
    if (persistedState) {
      // rehidrate formik
      return JSON.parse(persistedState);
    }
    const behaviour = {} as any;

    Categories.forEach(cat => {
      behaviour[cat.value] = 0;
    });

    const initialValues = {
      isBehaviourCompleted: 0, // Dummy value for behaviour question validation
      behaviour,
    } as any;

    return initialValues;
  };

  /**
   * Create the validation schema
   * @returns Yup validation schema
   */
  const getValidationSchema = () => {
    let validationSchema = {} as any;
    if (!state.metaData.isBaseline) {
      validationSchema = {
        [customQuestions[0].id]: Yup.number()
          .required(t('Please select how are you feeling this week'))
          .min(1, t('Please select how are you feeling this week')),
      };
    }
    wizard.steps.forEach(step => {
      if (step.isCustom) return;
      validationSchema[step.id] = Yup.string().required(
        'Please answer the question to continue'
      );
    });
    return validationSchema;
  };

  const formik = useFormik({
    initialValues: getInitialValues(),
    validationSchema: Yup.object().shape(getValidationSchema()),
    onSubmit: async (values: FormikValues) => {
      const surveys: Survey[] = [];
      const mutatedValues = { ...values };
      delete mutatedValues.isBehaviourCompleted;
      if (state.metaData.isBaseline) {
        delete mutatedValues.behaviour;
      }

      Object.keys(mutatedValues).forEach(key => {
        const step =
          steps.find(s =>
            s.type === 'behaviour' ? s.type === key : s.id === key
          ) || ({} as Step);
        surveys.push({
          id: step.id,
          type: step.type,
          response: mutatedValues[key],
          question: step.question,
        });
      });

      try {
        const student = state.studentProfile;
        delete student.createdAt;
        delete student.onboardingComplete;
        const apiPayload = {
          student,
          metadata: {
            ...state.metaData,
          },
          skip: false,
          answers: surveys,
        } as CreateSurveyResponse;
        await saveResponse(apiPayload);
        formik.resetForm();
        history.push('/thank-you');
      } catch (e) {
        formik.setSubmitting(false);
        toast.error(t('Error saving your response please try again'));
      }
    },
  });

  // Helper to check if form data has been persisted, to work with watch animation
  useEffect(() => {
    setLocalStorage(LocalStorage.FORM_STATE, JSON.stringify(formik.values));
  }, [formik.values]);

  /**
   * Get validation error message for the current step
   * @returns {string } Error message
   */
  const getStepError = () => {
    const step = wizard.steps[currentStep];
    if (step.type === FieldTypesConst.BEHAVIOUR) {
      return (formik.errors.isBehaviourCompleted as string) || '';
    }

    return (formik.errors[wizard.steps[currentStep]?.id] as string) || '';
  };

  /**
   * Callback bandler for the next button
   */
  const handleNext = () => {
    const step = wizard.steps[currentStep];
    if (
      step.type === FieldTypesConst.BEHAVIOUR &&
      formik.values.isBehaviourCompleted === 0
    ) {
      formik.setFieldTouched('behaviour');
      formik.setFieldError(
        'isBehaviourCompleted',
        t('Please answer all of the categories')
      );
      return;
    }
    if (
      step.type !== FieldTypesConst.BEHAVIOUR &&
      (!formik.touched[wizard.steps[currentStep].id] || getStepError())
    ) {
      formik.setFieldTouched(wizard.steps[currentStep].id);
      return;
    }

    if (currentStep === 0 && formik.values[customQuestions[0].id] === 1) {
      setLocalStorage(LocalStorage.CURRENT_STEP, (currentStep + 1).toString());
      history.push('/support-1');
      return;
    }

    if (!isLastStep) {
      onChange(currentStep + 1);
      return;
    }

    formik.handleSubmit();
  };

  /**
   * Callback bandler for the previous button
   */
  const handlePrevious = () => {
    onChange(currentStep - 1);
  };

  const updaeFormValue = useCallback(
    (field, value) => {
      formik.setFieldTouched(field);
      formik.setFieldValue(field, value);
    },
    [formik]
  );
  const currentStepData = wizard.steps[currentStep];
  return (
    <StyledRoot>
      <FormBody>
        <VerticalLayout childGap={spacing.large}>
          {steps.map((step, index) => (
            <WizardStep
              value={
                formik.values[
                  currentStepData.type === 'behaviour'
                    ? currentStepData.type
                    : currentStepData.id
                ]
              }
              shouldRender={currentStep === index}
              setFieldValue={updaeFormValue}
              key={step.id}
              step={step}
            />
          ))}

          <Typography color="red">
            {formik.touched[
              currentStepData.type === 'behaviour'
                ? currentStepData.type
                : currentStepData.id
            ]
              ? getStepError()
              : ''}
          </Typography>
        </VerticalLayout>

        <ButtonsContainer>
          {!state.metaData.isBaseline && currentStep === 0 && (
            <HorizontalLayout>
              <Button variant="link" onClick={() => history.push('/support-2')}>
                {t(`I don't want to do the check-in this week`)}
              </Button>
            </HorizontalLayout>
          )}
          <HorizontalLayout
            justify={currentStep > 0 ? 'center' : 'flex-end'}
            childGap={spacing.medium}
            marginTop={spacing.xxxLarge}
          >
            <ActivityIndicator active={formik.isSubmitting}>
              {currentStep > 0 && (
                <Button responsive variant="secondary" onClick={handlePrevious}>
                  {t('Previous')}
                </Button>
              )}
              <Button responsive onClick={handleNext}>
                {isLastStep ? t('Submit') : t('Next')}
              </Button>
            </ActivityIndicator>
          </HorizontalLayout>
        </ButtonsContainer>
      </FormBody>
    </StyledRoot>
  );
};

export default SurveyWizard;
