import React from 'react';
import classNames from 'classnames';
import { Clickable, Typography } from '../../ui';
import {
  StyledContainer,
  StyledImage,
  StyledImageContainer,
  StyledLabelContainer,
} from './styles';

type CardDataType = {
  value: number;
  label: string;
  icon?: string;
};

type BehaviourFieldType = {
  [key: string]: number | undefined;
};

type StyleProps = {
  color: string;
  borderColor?: string;
  bgActiveColor?: string;
  bgColor?: string;
  shadowColor?: string;
  labelContainer?: React.ElementType;
  container?: React.ElementType;
};

type CardProps = {
  onSelect: (number: number) => void;
  data: CardDataType;
  value: number | string | BehaviourFieldType;
  styles: StyleProps;
};

const Card: React.FC<CardProps> = ({ data, value, styles, onSelect }) => {
  const {
    color,
    bgColor = color,
    borderColor = color,
    bgActiveColor = color,
    shadowColor = color,
    labelContainer: LabelContainer,
    container: Container,
  } = styles;
  const CardWrapper = Container || StyledContainer;
  return (
    <Clickable onClick={() => onSelect(data.value)}>
      <CardWrapper
        className={classNames({ active: data.value === value })}
        color={color}
        bgColor={bgColor}
        bgActiveColor={bgActiveColor}
        borderColor={borderColor}
        shadowColor={shadowColor}
        active={data.value === value}
      >
        {data.icon && (
          <StyledImageContainer>
            <StyledImage src={data.icon} alt={data.label} />
          </StyledImageContainer>
        )}
        {LabelContainer ? (
          <LabelContainer>{data.label}</LabelContainer>
        ) : (
          <StyledLabelContainer>
            <Typography align="center" size="large">
              {data.label}
            </Typography>
          </StyledLabelContainer>
        )}
      </CardWrapper>
    </Clickable>
  );
};

export default Card;
